import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actionTypes from '../../../../store/actions';
import Checkmobile from '../../../utils/services/checkmobile/Checkmobile';
import { redirectToDashboard } from '../../../utils/services/utils';

class LectureHeader extends Component {
    mounted = false;

    state = {
        currLecture: null,
        currLectureId: 0
    }

    componentDidMount() {
        this.mounted = true;
        if (this.mounted && this.props.currLecture && +this.props.currLecture.id !== +this.state.currLectureId) {
            this.setState({
                currLecture: { ...this.props.currLecture },
                currLectureId: this.props.currLecture.id
            });
            this.props.mouseLeaveHandler();
        }
    }

    componentDidUpdate(prevProps) {
        this.mounted = true;
        if (this.mounted && this.props.currLecture && (this.props.currLecture.id !== prevProps.currLecture.id)) {
            this.setState({
                currLecture: { ...this.props.currLecture },
                currLectureId: this.props.currLecture.id
            });
        }
    }

    componentWillUnmount() {
        this.mounted = false;
    }

    curriculumToggleHandler = () => {
        this.props.onCurriculumToggle();
    }

    backToDashboard = () => {
        if (Checkmobile()) {
            this.props.menuOpenFn();
        } else {
            return redirectToDashboard(this.props.course.id, this.props.isPreview, this.props.courseSlug);
        }
    }

    render() {
        let lectureHeader = null;
        let hideData = true;
        if (this.state.currLecture) {
            let classes = ['nav-container'];

            if ([3, 8].indexOf(+this.state.currLecture.cl_lecture_type) !== -1) {
                classes.push('nav-height');
            }
            let lectureText = 'Lecture';
            switch (+this.state.currLecture.cl_lecture_type) {
                case 13:
                    lectureText = 'Survey';
                    break;
                case 3:
                    lectureText = 'Quiz';
                    break;
                case 8:
                    lectureText = 'Assignment';
                    break;
                default:
                    lectureText = 'Lecture';
            }

            if ([5, 13, 3, 8].indexOf(+this.state.currLecture.cl_lecture_type) !== -1) {
                hideData = false;
            }

            lectureHeader = <div className={classes.join(' ')} onMouseEnter={this.props.mouseEnterHandler} onMouseLeave={this.props.mouseLeaveHandler}>
                <div className='nav-header' style={{ background: +this.state.currLecture.cl_lecture_type === 2 ? null : 'none', opacity: this.props.opacity, visibility: this.props.opacity ? 'visible' : 'hidden' }}>
                    <div className="menu-icon">
                        <span className="menu" onClick={this.curriculumToggleHandler}></span>
                    </div>
                    {((hideData || this.props.locked) && this.state.currLecture) ? <div className="text-left contenttitle">
                        <span className="lecture-title">{this.state.currLecture ? this.state.currLecture.cl_lecture_name : null}</span>
                        <div className="curriculam-index">
                            <span>Section : {this.state.currLecture ? this.state.currLecture.sNumber : null} &nbsp;</span>
                            <span>{lectureText} : {this.state.currLecture ? this.state.currLecture.lNumber : null}</span>
                        </div>
                    </div> : null}
                    <div title="Back to course dashboard." className="text-right ask-wrapper">
                        <span className="close-icon" onClick={this.backToDashboard}></span>
                    </div>
                </div>
            </div>;
        }

        return lectureHeader;
    }
}

function mapStateToProps(state) {
    return {
        currLecture: state.currLecture,
        currOpen: state.currOpen,
        course: state.course,
        opacityClass: state.opacityClass,
        isPreview: state.isPreview,
        courseSlug: state.course.cb_slug
    };
};

function mapDispatchToProps(dispatch) {
    return {
        onCurriculumToggle: () => dispatch({ type: actionTypes.TOGGLE_CURRICULUM }),
        menuOpenFn: () => dispatch({ type: actionTypes.SET_MENU_OPEN, payload: true }),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(LectureHeader);