import  React,{Component} from 'react';
import * as actionTypes from '../../../store/actions';
import {connect} from 'react-redux';

class Section extends Component{
    _handleClick = () => {
        this.props.onSectionToggle(this.props.sectionId);
    }
    render(){
        return(
            <div onClick={this._handleClick} className="curriculam-title curriculam-accordion">
                <h4>{this.props.sectionName}</h4>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
	return {
		onSectionToggle: (sectionId) => dispatch({type :actionTypes.TOGGLE_SECTION,payload : {sectionId : sectionId}})
	};
}

export default connect(null,mapDispatchToProps)(Section);