import React,{ Component } from 'react';
import {connect} from 'react-redux';

class StickyHeader extends Component {
    mounted = false;

    state = {
        currLecture : null,
        currLectureId : 0
    }

    componentDidMount(){
        this.mounted = true;
        if(this.mounted && this.props.currLecture && +this.props.currLecture.id !== +this.state.currLectureId){
            this.setState({
                currLecture : {...this.props.currLecture},
                currLectureId : this.props.currLecture.id
            });
            // this.props.mouseLeaveHandler();
        }
    }

    componentDidUpdate(prevProps){
        this.mounted = true;
        if(this.mounted && prevProps.currLecture){
            if(this.props.currLecture.id !== prevProps.currLecture.id) {
                this.setState({
                    currLecture : {...this.props.currLecture},
                    currLectureId : this.props.currLecture.id
                });
            }
        }
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    render (){
        let lectureHeader = null;

        if(this.state.currLecture){
            let lectureText = 'Lecture';
            switch(+this.state.currLecture.cl_lecture_type){
                case 13:
                lectureText = 'Survey';
                break;
                case 3:
                lectureText = 'Quiz';
                break;
                case 8:
                lectureText = 'Assignment';
                break;
                default:
                lectureText = 'Lecture';
            }

            lectureHeader = <div className="scrollable-title-wrapper">
                                <div className="content-title">{this.state.currLecture?this.state.currLecture.cl_lecture_name:null}</div>
                                <div className="content-title-index">
                                    <span className="section-count">Section :{this.state.currLecture?this.state.currLecture.sNumber:null}</span>
                                    <span className="topic-count">{lectureText} : {this.state.currLecture?this.state.currLecture.lNumber:null}</span>
                                </div>
                            </div>;
        }

        return lectureHeader;
    }
}

const mapStateToProps = state => {
    return {
        currLecture : state.currLecture,
        currOpen : state.currOpen,
        course : state.course
    };
};

export default connect(mapStateToProps,null)(StickyHeader);