import React from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import * as actionTypes from '../../store/actions';
import './Forum.css';

class Forum extends React.Component {
    render(){
        const config = this.props.config;
        const tokenString = this.props.token ? `?token=${this.props.token}` : '';
        return (
            <div className="discussion-right-wrapper">
				<div className="discussion-right-head">
					<span className="discussion-right-title">Question &amp; Answers</span>
					<span onClick={this.props.onDiscussionToggle} className="close-icon"></span>
				</div>
                <div className="discusion-container white-bg">
                    <iframe title="Forum" frameBorder="0" style={{height:'calc(100vh - 66px)'}} width={'100%'} src={`${config.apiUrl}/forum_service/${this.props.match.params.courseId}/${this.props.match.params.lectureId}/notmain${tokenString}`}></iframe>
                </div>
			</div>
        );
    }
}

const mapStateToProps = state => {
    return {
        config: state.config,
        token: state.token
    }
}

const mapDispatchToProps = dispatch => {
	return {
		onDiscussionToggle: () => dispatch({type :actionTypes.TOGGLE_DISCUSSION})
	};
}

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Forum));