import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actionTypes from '../../../store/actions';

class SupportDoc extends Component {
    mounted = false;
    state = { materialUrl: '' }

    formatUrl() {
        let materialUrl = this.props.config.materialUrl;
        if(this.props.courseId && materialUrl) {
            materialUrl = materialUrl.replace('{courseId}', this.props.courseId);
            materialUrl = materialUrl.replace('{purpose}', 'support_files');
            this.setState({ materialUrl });
        }
    }

    componentDidMount() {
        this.mounted = true;
        this.formatUrl();
    }

    componentDidUpdate(prevProps) {
        if(this.mounted && (prevProps.courseId !== this.props.courseId)) {
            this.formatUrl();
        }
    }

    componentWillUnmount() {
        this.mounted = false;
    }

    _handleClick = () => {
        this.props.onShowList();
    }

    _listDocs = (filesArray) => {
        return filesArray.map((value, index) => {
            const supportFileUrl = this.state.materialUrl.replace('{file}', value.file_raw_name);
            let fileNameSplit = value.file_name.split('.');
            fileNameSplit.pop();
            const orgFileName = fileNameSplit.join('');
            
            return (
                <li key={index}>
                    <a href={supportFileUrl} download={orgFileName} target="_blank" rel="noopener noreferrer">
                        <span className="download-icon"><svg width="24" height="24" viewBox="0 0 24 24"><g><rect fill="none" id="canvas_background" height="402" width="582" y="-1" x="-1"></rect></g><g><path id="svg_1" d="m11,14.59l0,-11.59a1,1 0 0 1 2,0l0,11.59l3.3,-3.3a1,1 0 0 1 1.4,1.42l-5,5a1,1 0 0 1 -1.4,0l-5,-5a1,1 0 0 1 1.4,-1.42l3.3,3.3zm-8,2.41a1,1 0 0 1 2,0l0,3l14,0l0,-3a1,1 0 0 1 2,0l0,3a2,2 0 0 1 -2,2l-14,0a2,2 0 0 1 -2,-2l0,-3z" className="heroicon-ui"></path></g></svg></span>

                        {value.file_name}
                    </a>
                </li>
            )
        })
    }

    render() {
        try {
            let { currentLecture } = this.props;
            if(currentLecture) {
                // currentLecture.cl_support_files = {"a437370f491fec3a462e2ce0d3fadd4a":{"file_name":"questiontemplate_(5)_(1).xls","file_raw_name":"a437370f491fec3a462e2ce0d3fadd4a.xls"},"eaded001a74c501ed2cb199634f59525":{"file_name":"questiontemplate_(2).docx","file_raw_name":"eaded001a74c501ed2cb199634f59525.docx"},"25bb228173a6a70292eb53341d2e3d1b":{"file_name":"SampleVideo_1280x720_1mb.mp4","file_raw_name":"25bb228173a6a70292eb53341d2e3d1b.mp4"}};
                // console.log(currentLecture.cl_support_files);
                let supportFiles = currentLecture.cl_support_files;
                supportFiles = supportFiles ? JSON.parse(supportFiles) : {};
                delete supportFiles[""];
                const filesArray = Object.values(supportFiles);

                if(filesArray.length > 0) {
                    return (
                        <div className={this.props.showList ? "supported-docs" : ""}>
                            <div className={this.props.showList ? "list-open" : "list-close"}>
                                <ul>
                                    <li className="doc_ul_title"><span>Supported Documents</span><span className="close-doc" onClick={this._handleClick}>&times;</span></li>
                                    <div className="custom-scroller overflow-list">{this._listDocs(filesArray)}</div>
                                </ul>
                            </div>
                        </div>
                    )
                }
            }
        } catch(e) {
            console.log(e)
        }

        return null
    }
}

function mapStateToProps(state) {
    return {
        config: state.config,
        currentLecture : state.currLecture,
        showList: state.showList,
        courseId: state.subscription.cs_course_id
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onShowList: () => dispatch({ type: actionTypes.SHOW_LIST })
        
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(SupportDoc);