import React,{ Component } from 'react';
import {withRouter} from 'react-router-dom';
import * as actionTypes from '../../store/actions';
import Checkmobile from '../../components/utils/services/checkmobile/Checkmobile';
import Curriculum from './curriculum/Curriculum';
import Content from './content/Content';
import Forum from '../../components/forum/Forum';
import Menu from './Menu';
import './Modal.css';
import {redirectToDashboard} from '../../components/utils/services/utils';

import {connect} from 'react-redux';

const fullScreenValue = localStorage.getItem('full_screen');

class Main extends Component {
    mounted = false;

    state = {
        lectureId : 0,
        showModal : ((fullScreenValue === 'false') ? false : true)
    }

    componentDidMount(){
        this.mounted = true;

        if(this.mounted && this.props.match.params.lectureId && this.props.curriculum.sections){
            this.setState({
                lectureId : +this.props.match.params.lectureId
            },() => {
                this.props.lectureSwitch(+this.props.match.params.lectureId);
                if(Checkmobile() && this.props.currOpen){ 
                    this.props.onCurriculumToggle();
                }
            });
        }
    }

    componentDidUpdate(prevProps){
        if(this.mounted && this.props.match.params.lectureId !== 'undefined' && (prevProps.match.params.lectureId !== this.props.match.params.lectureId)){
            this.setState({
                lectureId : +this.props.match.params.lectureId
            },() => {
                this.props.lectureSwitch(+this.props.match.params.lectureId);
                if(Checkmobile() && this.props.currOpen){ 
                    this.props.onCurriculumToggle();
                }
            });
        }
    }

    cancelFull = () => {
        if(this.mounted){
            localStorage.setItem('full_screen', 'false')
            this.setState({
                showModal : false
            });
        }
    }

    continueFull = () => {
        if(this.mounted){
            localStorage.setItem('full_screen', 'true')
            this.setState({
                showModal : false
            },() => {
                let el = document.documentElement , rfs = el.requestFullScreen || el.webkitRequestFullScreen || el.mozRequestFullScreen ; rfs.call(el); 
            });
        }
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    render(){
        let classes = ['row','content-delivery-wrapper'];

        if(this.props.isPreview && this.props.previewTimer <= 0){
            redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
            return null;
        }

        if(this.mounted && this.props.currOpen){
            classes[classes.length] = 'curriculam-toggle';
        }

        if(this.mounted && this.props.dissOpen){
            classes[classes.length] = 'discussion-toggle';
        }

        if(this.mounted && this.props.showList){
            classes[classes.length] = 'supportdoc-toggle';
        }

        let modalClasses = ['modal-container'];

        if(this.state.showModal){
            // modalClasses.push('visible');
        } else {
            modalClasses = [modalClasses[0]];
        }

        return(
            <div>
                <div className="container-fluid">
                    <div className={classes.join(' ')}>
                        <Curriculum/>
                        <Content/>
                        {!this.props.isPreview && <Forum lectureId={this.props.match.params.lectureId}/>}
                    </div>
                    <div className={modalClasses.join(' ')}>
                        <div className="modal-wrapper">
                            <div className="popup-content">
                                <h2>Continue fullscreen for better experience !</h2>
                                <div className="confirm-btn-wrapper">
                                    <button className="pop-btn-cancel" onClick={this.cancelFull} type="button">Cancel</button>
                                    <button className="pop-btn-ok" onClick={this.continueFull} type="button">Continue</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Menu onfullScreenClick={this.continueFull} />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        currOpen : state.currOpen?state.currOpen:false,
        dissOpen : state.dissOpen?state.dissOpen:false,
        curriculum : state.curriculum,
        showList: state.showList,
        previewTimer: state.previewTimer,
        isPreview: state.isPreview,
		courseSlug: state.course.cb_slug
    }
}

const mapDispatchToProps = dispatch => {
    return {
        lectureSwitch: (lectureId) => dispatch({type : actionTypes.SWITCH_LECTURE,payload : {lectureId : lectureId}}),
        onCurriculumToggle: () => dispatch({ type: actionTypes.TOGGLE_CURRICULUM }),
        setPreview: () => dispatch({ type: actionTypes.SET_PREVIEW }),
    };
};

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Main));