import React from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import './Survey.css';
import Axios from '../../../axios/php';
import * as actionTypes from '../../../store/actions';
import Aux from '../../../hoc/Aux/Aux';

import Single from './single/Single';
import Multiple from './multiple/Multiple';
import Dropdown from './dropdown/Dropdown';
import Text from './text/Text';
import Range from './range/Range';

import DocumentHeader from '../document/documentHeader/DocumentHeader';
import DocumentFooter from '../document/documentFooter/DocumentFooter';
import StickyHeader from '../assignment/stickyHeader/StickyHeader';
import { redirectToDashboard } from '../../utils/services/utils';

import CheckImg from '../../../assets/images/check.svg';

class Survey extends React.Component {
    mounted = false;

    state = {
        activeTab: 0,
        opacity: 1,
        timer: null,
        courseDetails : null,
        surveyData : null,
        surveyQuestions : null,
        response : {},
        submitable : false,
        submiting : false,
        showSuccess : null,
        loading : true
    }

    componentDidMount() {
        this.mounted = true;

        if (this.mounted) {
            this.setSurveyDetails();
        }

        this.mouseLeaveHandler();
    }

    componentDidUpdate(prevProps,prevState){
        if(this.mounted && +prevProps.lectureId !== +this.props.lectureId){
            this.setState({
                surveyData : null,
                surveyQuestions : null,
                response : {},
                submitable : false,
                submiting : false,
                loading : true
            },() => {
                this.setSurveyDetails();
            });
        }
    }

    setSurveyDetails = () => {
        const request = {
            header : {
                service: "",
                method: "lecture_detail"
            },
            body : {
                course_id : this.props.match.params.courseId,
                lecture_id : this.props.lectureId,
                lecture_type : 13
            }
        };

        Axios.post(`/content`,JSON.stringify(request))
        .then(response => {
            if(!response.data.header.error){
                if(this.mounted){
                    this.setState({
                        surveyData : {...response.data.body.lecture.survey},
                        surveyQuestions : {...response.data.body.lecture.questions},
                        courseDetails : this.props.courseDetails,
                        loading : false
                    },()=>{
                        // console.log(this.state);
                    })
                }
            }else{
                if(response.data.header.code === 604){
                    return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
                }else{
                    //console.log(response.data.header.message);
                }
            }
        })
        .catch(err => {
            //console.log(err);
        });
    }

    mouseEnterHandler = () => {
        if (this.state.timer) {
            clearTimeout(this.state.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        // if (this.mounted && !this.props.currOpen) {
        //     const timer = setTimeout(() => {
        //         if (this.mounted) {
        //             this.setState({
        //                 opacity: 0
        //             });
        //         }
        //     }, 4000);

        //     this.setState({
        //         timer: timer
        //     });
        // }
    }

    componentWillReceiveProps(nextProps){
        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.mouseEnterHandler();
            }else{
                this.mouseLeaveHandler();
            }
        }
    }

    componentWillUnmount() {
        this.mounted = false;
    }

    recieveResponse = (respObj) => {
        let response = {...this.state.response};
        response[respObj.id] = respObj;

        switch(respObj.type){
            case 2:
                if(!respObj.answer.length){
                    delete response[respObj.id];
                }
            break;
            case 3:
                if(typeof respObj.answer === 'undefined'){
                    delete response[respObj.id];
                }
            break;
            case 4:
                if(respObj.answer === ''){
                    delete response[respObj.id];
                }
            break;
            default:
        }

        this.setState({
            response : response
        });
    }

    submitHandler = () => {
        let questions = {...this.state.surveyQuestions};
        let submittable = true;
        let answers = [];
        for (var question in questions) {
            questions[question].validation = null;
            let respObj = typeof this.state.response[questions[question].id] !== 'undefined'?{...this.state.response[questions[question].id]}:null;

            if(respObj){
                let answerObj = {};
                answerObj.question_id = respObj.id;
                answerObj.question = respObj.question;
                switch(respObj.type){
                    case 1:
                        answerObj.opinion = [{answer:respObj.answer}];
                    break;
                    case 2:
                        let multiObj = respObj.answer.map((answer) => {
                            return {answer : answer}
                        });
                        answerObj.opinion = multiObj;
                    break;
                    case 3:
                        answerObj.opinion = [{answer:respObj.answer}];
                    break;
                    case 4:
                        answerObj.opinion = [{answer:respObj.answer}];
                    break;
                    case 5:
                        answerObj.opinion = [{answer:respObj.answer}];
                    break;
                    default:
                }

                answers.push(answerObj);
            }

            if(+questions[question].sq_required){
                switch(+questions[question].sq_type){
                    case 1:
                        if(!respObj || respObj.answer === ''){
                            submittable = false;
                            questions[question].validation = 'This is a mandatory field.';
                        }
                    break;
                    case 2:
                        if(!respObj || !respObj.answer.length){
                            submittable = false;
                            questions[question].validation = 'This is a mandatory field.';
                        }
                    break;
                    case 3:
                        if(!respObj || typeof respObj.answer === 'undefined'){
                            submittable = false;
                            questions[question].validation = 'This is a mandatory field.';
                        }
                    break;
                    case 4:
                        if(!respObj || respObj.answer === ''){
                            submittable = false;
                            questions[question].validation = 'This is a mandatory field.';
                        }
                    break;
                    case 5:
                        if(!respObj || respObj.answer === ''){
                            submittable = false;
                            questions[question].validation = 'This is a mandatory field.';
                        }
                    break;
                    default:
                }
            }
        }

        if(submittable && !this.state.submiting){
            this.setState({
                submitable : submittable,
                submiting : true
            },() => {
                const request = {
                    header : {
                        method: "save_user_survey"
                    },
                    body : {
                        course_id : this.props.match.params.courseId,
                        lecture_id : this.props.lectureId,
                        lecture_type : 13,
                        survey_id : this.state.surveyData.survey_id,
                        course_name : this.state.courseDetails.cb_title,
                        survey_name : this.state.surveyData.s_name,
                        tutor_id : this.state.surveyData.s_tutor_id,
                        details : answers
                    }
                };
        
                Axios.post(`/content`,JSON.stringify(request))
                .then(response => {
                    if(!response.data.header.error){
                        if(this.mounted){
                            // this.props.onPercentageUpdate({
                            //     percentage : 100,
                            //     cue : 0,
                            //     lectureId : this.props.lectureId,
                            //     completed : true
                            // });
                            this.setState({
                                submiting : false,
                                showSuccess : true
                            },() => {
                                setTimeout(() => {
                                    this.props.onPercentageUpdate({
                                        percentage : 100,
                                        cue : 0,
                                        lectureId : this.props.lectureId,
                                        completed : true
                                    });
                                    let lectureId = 0;
                                    if(typeof this.props.currLecture.prevLecture.id !== 'undefined'){
                                        lectureId = this.props.currLecture.prevLecture.id;
                                    }
                                    if(typeof this.props.currLecture.nexLecture.id !== 'undefined'){
                                        lectureId = this.props.currLecture.nexLecture.id;
                                    }
                                    this.props.history.replace(`/${this.props.match.params.courseId}/${lectureId}`);
                                }, 2000)
                                // let lectureId = 0;
                                // if(typeof this.props.currLecture.prevLecture.id !== 'undefined'){
                                //     lectureId = this.props.currLecture.prevLecture.id;
                                // }
                                // if(typeof this.props.currLecture.nexLecture.id !== 'undefined'){
                                //     lectureId = this.props.currLecture.nexLecture.id;
                                // }
                                // setTimeout(() => {
                                //     if(this.mounted){
                                //         this.setState({
                                //             showSuccess : false
                                //         },() => {
                                //             this.props.history.replace(`/${this.props.match.params.courseId}/${lectureId}`);
                                //         });
                                //     }
                                // },2000);
                            });
                        }
                    }else{
                        if(response.data.header.code === 604){
                            return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
                        }else{
                            //console.log(response.data.header.message);
                        }
                    }
                })
                .catch(err => {
                    //console.log(err);
                });
            });
        }else{
            this.setState({
                surveyQuestions : {...questions}
            });
        }
    }

    lectureSwitch = (params) => {
        const pathname = `/${this.props.match.params.courseId}/${params.lectureId}`;
        this.props.history.push({
            pathname: pathname,
            state: { action : params.direction }
        })
    }

    render() {
        let nextLecture = null;
        let containerClasses = "context-holder document-context survey-container";
        let questions = this.state.surveyQuestions?this.state.surveyQuestions:{};
        if(this.props.currLecture && this.props.currLecture.nexLecture && typeof this.props.currLecture.nexLecture.id !== 'undefined'){
            nextLecture = this.props.currLecture.nexLecture;
        }
        
        let queObj = [];
        for (var question in questions) {
            let ret = null;
            switch(+questions[question].sq_type){
                case 1:
                ret = <Single 
                    key={question}
                    no={+question+1}
                    data={questions[question]}
                    recieveResponse={(data) => this.recieveResponse(data)}
                    validation={typeof questions[question].validation !== 'undefined'?questions[question].validation:null}
                />
                break;
                case 2:
                ret = <Multiple
                    key={question}
                    no={+question+1}
                    data={questions[question]}
                    recieveResponse={(data) => this.recieveResponse(data)}
                    validation={typeof questions[question].validation !== 'undefined'?questions[question].validation:null}
                />
                break;
                case 3:
                ret = <Dropdown 
                    key={question}
                    no={+question+1}
                    data={questions[question]}
                    recieveResponse={(data) => this.recieveResponse(data)}
                    validation={typeof questions[question].validation !== 'undefined'?questions[question].validation:null}
                />
                break;
                case 4:
                ret = <Text 
                    key={question}
                    no={+question+1}
                    data={questions[question]}
                    recieveResponse={(data) => this.recieveResponse(data)}
                    validation={typeof questions[question].validation !== 'undefined'?questions[question].validation:null}
                />
                break;
                case 5:
                ret = <Range 
                    key={question}
                    no={+question+1}
                    data={questions[question]}
                    recieveResponse={(data) => this.recieveResponse(data)}
                    validation={typeof questions[question].validation !== 'undefined'?questions[question].validation:null}
                />
                break;
                default:
                ret = null;
            }
            queObj.push(ret);
        }

        let content =   <Aux>
            <div className="survey-row">
                <div className="survey-title-info text-center">
                    <h2>{this.state.surveyData?this.state.surveyData.s_name:null}</h2>
                </div>
            </div>
            {queObj}
            <div className="survey-submit text-center">
                <button onClick={this.submitHandler} className="btn-violet" type="button">
                    {this.state.submiting?'Submiting...':'Submit'}
                </button>
            </div>
        </Aux>

        if(this.state.showSuccess){
            containerClasses = "context-holder vcenter survey-container";
            content = <div>
                <div className="text-center">
                    <img height="100" src={CheckImg} alt="Success"/>
                </div>
                <p className="text-message">Thank you for the feedback.</p>
            </div>
        }

        if(typeof this.props.lectureLog.log[+this.props.lectureId] !== 'undefined'){
            containerClasses = "context-holder vcenter survey-container";
            content = <div>
                <div className="text-center">
                    <img height="100" src={CheckImg} alt="Success"/>
                </div>
                <p className="text-message">Survey already submitted.</p>
                <div className="text-center btn-container">
                    {nextLecture && <button className="btn-violet" onClick={() => {this.lectureSwitch({direction:'forward',lectureId:nextLecture.id})}} type="button">Continue</button>}
                </div>
            </div>
        }

        return(
            <div className="preview-area custom-scroller">
                <StickyHeader/>
                <DocumentHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />

                <div className="lecture-holder">
                    <div className={containerClasses}>
                        <div className="context">
                            <div className="survey-wrapper">
                                {this.state.loading?<span className="result-success">Loading survey...</span>:content}
                            </div>
                        </div>
                    </div>
                </div>

                <DocumentFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        courseDetails : state.course,
        lectureId : state.currLecId,
        currLecture : state.currLecture,
        lectureLog : state.lectureLog,
        isPreview: state.isPreview,
        courseSlug: state.course.cb_slug
    }
}

const mapDispatchToProps = dispatch => {
    return {
		onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData})
	};
}

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Survey));