import React, {Component} from 'react';
import Vimeo from '@u-wave/react-vimeo';
import {connect} from 'react-redux';
import * as actionTypes from '../../../store/actions';
import './Vimeo.css';
import VideoHeader from './videoHeader/VideoHeader';
import VideoFooter from './videoFooter/VideoFooter';
import {getPreviewDetails} from '../../utils/services/utils';

class Video extends Component {

    mounted = false;
    timer = null;

    state = {
        currLecture : null,
        timer : 0,
        cue : 0,
        percentage : 0,
        swipeEvent : false,
        opacity: 1
    }

    componentDidMount(){
        this.mounted = true;

        if(this.mounted && this.props.currLecture){
            this.setState({
                currLecture : {...this.props.currLecture},
                cue : this.props.currLecture.cue
            });
        }
        this.mouseLeaveHandler();
        getPreviewDetails(this.props.courseId, this.props.isPreview, this.props.courseSlug).catch(console.log)
    }

    componentDidUpdate(){
        if(this.mounted && typeof this.props.currLecture.id !== 'undefined' && this.state.currLecture && +this.props.currLecture.id !== +this.state.currLecture.id){
            this.setState({
                currLecture : {...this.props.currLecture},
                cue : this.props.currLecture.cue
            });
        }
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    onPercentageUpdate = (data) =>{
        let percentage = Math.ceil(data.percent*100);
        let prevTimer = this.state.timer;
        if(data.seconds % 5 !== 0 && this.mounted){
            this.setState({
                timer : Math.ceil(data.seconds),
                cue : data.seconds,
                percentage : percentage
            },() => {
                if(prevTimer !== this.state.timer || percentage === 100){
                    this.props.onPercentageUpdate({
                        percentage : percentage,
                        cue : Math.ceil(data.seconds),
                        lectureId : this.state.currLecture.id,
                        completed : data.completed
                    });
                }
            });
        }

        if(data.completed && this.mounted){
            this.props.onComplete();
        }
    }

    componentWillReceiveProps(nextProps)
    {
        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            this.setState({
                swipeEvent : nextProps.swipeEvent
            });
        }
    }

    mouseEnterHandler = () => {
        if (this.timer) {
            clearTimeout(this.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        if (this.mounted && !this.props.currOpen && !this.props.isPreview) {
            this.timer = setTimeout(() => {
                if (this.mounted) {
                    this.setState({
                        opacity: 0
                    });
                }
            }, 4000);
        }
    }
    
    render(){
        const videoHeight = window.innerHeight - 60 * 2;
        let videoId = (this.state.currLecture && this.state.currLecture.cl_filename)?this.state.currLecture.cl_filename.split('/'):null;
        videoId = videoId ? videoId[videoId.length - 1] : null;
        videoId = videoId ? videoId.split('?')[0] : null;
        let videoBlock = null;
        if(videoId){
            videoBlock =    <Vimeo
                                id={'vimeo'}
                                video={videoId}
                                className="vimeo video-container"
                                height={(this.state.opacity === 1) ? videoHeight : '100%'}
                                autoplay={this.props.autoPlay}
                                start={this.state.cue}
                                volume={0.5}
                                // paused={this.props.dissOpen || this.props.currOpen}
                                onTimeUpdate={(data) => this.onPercentageUpdate({...data,completed:false})}
                                onEnd={(data) => {this.onPercentageUpdate({...data,completed:true})}}
                                // onPause={(data) => console.log('Pause : ',data)}
                                // onPlay={(data) => console.log('Play :',data)}
                            />
        }

        return (
            <div style={{backgroundColor: '#000'}} className="video-lecture">
                <div className="active-topbar" onMouseEnter={this.mouseEnterHandler} onMouseLeave={this.mouseLeaveHandler}></div>
                <VideoHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent}/>
                {videoBlock}
                <VideoFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent} />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        currLecture : state.currLecture,
        currOpen : state.currOpen,
        dissOpen : state.dissOpen,
        isPreview: state.isPreview,
		courseSlug: state.course.cb_slug
    };
};

const mapDispatchToProps = dispatch => {
    return {
		onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData})
	};
};

export default connect(mapStateToProps,mapDispatchToProps)(Video);