import React,{ Component } from 'react';
import './Curriculum.css';
import {connect} from 'react-redux';
import {withRouter,Redirect} from 'react-router-dom';
import phpAxios from '../../../axios/php';
import * as actionTypes from '../../../store/actions';
import Head from '../../../components/curriculum/head/Head';
import Section from '../../../components/curriculum/section/Section';
import Lectures from '../../../components/curriculum/lectures/Lectures';
import Aux from '../../../hoc/Aux/Aux';
import {getPreviewDetails, redirectToDashboard} from '../../../components/utils/services/utils';

let resumeIds = false;
try{
	const resumeId = localStorage.getItem('resume_id');
	resumeIds = resumeId ? JSON.parse(resumeId) : false;
	resumeIds = (resumeIds instanceof Object) ? resumeIds : false;
} catch(e) {
	resumeIds = false
}

class Curriculum extends Component {
	mounted = null;
	previewTimerObj = null;

	state = {
		curriculum : [],
		currLoaded : false,
		currentLectureId : 0,
		currentSectionId : 0
	};

	getCurriculumData = () => {
		const request = {
			header : {
				service: "",
				method: "curriculum_list"
			},
			body : {
				course_id : this.props.match.params.courseId
			}
		};
		return phpAxios.post('/content',JSON.stringify(request))
	}

	savePreviewTime = () => {
		const request = {
			header : {
				service: "",
				method: "save_course_preview_time"
			},
			body : {
				course_id : this.props.match.params.courseId
			}
		};
		return phpAxios.post('/content',JSON.stringify(request))
		.then(response => {
			if(!response.data) {
				return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
			}
			if(!response.data.header.error){
			} else if(response.data.header.code === 604){
				return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
			}
			return response;
		});
	}

	componentDidMount(){
		this.mounted = true;

		if(this.mounted && !this.props.currLoaded){
			getPreviewDetails(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug)
			.then(response => {
					if(!response.data.header.error){
							const body = response.data.body;
							if(+body.course_preview === 1) {
									let prevTime = 0;
									if((+body.user_preview_time > 0) && (+body.course_preview_time > +body.user_preview_time)) {
											prevTime = parseInt(body.course_preview_time, 10) - parseInt(body.user_preview_time, 10);
									} else if((+body.user_preview_time === 0) && (+body.course_preview_time > 0)) {
											prevTime = +body.course_preview_time;
									}
									this.props.setPreviewTime(prevTime);
									this.props.setPreviewEnabled();
							}
					}
					return this.getCurriculumData();
			})
			.then(response => {
				if(!response.data.header.error){
					if(!response.data.body.curriculum.subscription){
						if(this.props.previewEnabled && (this.props.previewTimer > 0)) {
							this.props.setPreview();
							const subEndTime = new Date(Date.now() + 8.64e+7);
							const subEndMonth = subEndTime.getMonth() + 1;
							const subEndDate = subEndTime.getFullYear() + "-" + ((subEndMonth < 10) ? `0${subEndMonth}` : subEndMonth) + "-" + subEndTime.getDate();
							response.data.body.curriculum.subscription = { id: true, cs_course_id: this.props.match.params.courseId, cs_percentage: "0", cs_lecture_log: "", cs_last_played_lecture: "", cs_end_date: subEndDate };
						} else {
							return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
						}
					}
					const subscriptionEndDate = response.data.body.curriculum.subscription.cs_end_date ? new Date(`${response.data.body.curriculum.subscription.cs_end_date}T23:55:00`).getTime() : 0;
					if(subscriptionEndDate < Date.now()) {
						return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
					}
					if(this.props.isPreview) { 
							this.previewTimerObj = setInterval(() => {  
									this.props.setPreviewTime(false);
									if((this.props.previewTimer % 5 === 0) || (this.props.previewTimer === 3)) {
										this.savePreviewTime().catch(e => (redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug)));
									}
							}, 1000) 
					}
					if(this.props.match.params.lectureId) {
						response.data.body.curriculum.subscription.cs_last_played_lecture = this.props.match.params.lectureId;
					}
					let curriculum = {...response.data.body.curriculum.sections};
					let present = false;
					let fallbackLectureId;
					for (var skey in curriculum) {
						if (curriculum.hasOwnProperty(skey)) {
							if(!fallbackLectureId) { fallbackLectureId = +curriculum[skey].s_lectures[0].id }
							for (var lkey in curriculum[skey].s_lectures) {
								if ((typeof this.props.match.params.lectureId !== 'undefined' && (+curriculum[skey].s_lectures[lkey].id === +this.props.match.params.lectureId)) || (+response.data.body.curriculum.subscription.cs_last_played_lecture !== 0 && (+curriculum[skey].s_lectures[lkey].id === +response.data.body.curriculum.subscription.cs_last_played_lecture)) || +response.data.body.curriculum.subscription.cs_last_played_lecture === 0) {
									present = true;
								}
							}
						}
					}
					if(!present){
						// window.location = '/course/dashboard/'+this.props.match.params.courseId;
						response.data.body.curriculum.subscription.cs_last_played_lecture = fallbackLectureId;
					}

					let resData = response.data.body;
					let tab = this.props.match.params.tab;
					const cId = this.props.match.params.courseId;
					const scId = `cid_${cId}`;
					if((tab === 'resume') && resumeIds) {
						if(resumeIds[scId]) {
							resData.curriculum.subscription.cs_last_played_lecture = resumeIds[scId]
							const pathname = `/${cId}/${resumeIds[scId]}`;
							this.props.history.push(pathname)
						}
					}
					resumeIds = resumeIds ? resumeIds : {};
					resumeIds[scId] = +this.props.match.params.lectureId;
					localStorage.setItem('resume_id', JSON.stringify(resumeIds))

					this.props.onCurriculumSet({
						...resData
					});
				}else{
					return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
				}
			}).catch((error) => {
				// console.log(error);
				return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
			});
		}
	}

	componentWillReceiveProps(){
		if(this.mounted && this.props.curriculum){
			this.setState({
				currLoaded : true,
				currentLectureId : this.props.currentLectureId,
				currentSectionId : this.props.currentSectionId
			});
		}
	}

	componentWillUnmount(){
		this.mounted = false;
	}

	componentDidUpdate(prevProps,prevState){
		try {
			if(this.mounted && ((JSON.stringify(this.props.curriculum) !== JSON.stringify(prevProps.curriculum)) || (JSON.stringify(this.props.curriculum) !== JSON.stringify(this.state.curriculum)))){
				this.setState({
					curriculum : [...this.props.curriculum],
					currLoaded : true,
					currentLectureId : this.props.currentLectureId,
					currentSectionId : this.props.currentSectionId,
					subscription : this.props.subscription
				});
			}
		} catch(e) {}
	}

	render(){
		let sections = this.state.currLoaded?this.state.curriculum:[];
		sections = sections.map((section,s_key) => {
			if(section.id === this.state.currentSectionId) {
				section.open = true;
			}
			return (
				<Aux key={s_key}>
					{section.lectureCount?<Section key={s_key} sectionId={section.id} sectionName={section.s_name}/>:null}
					<Lectures courseId={this.state.subscription.cs_course_id} collapsed={!section.open} currentLectureId={this.state.currentLectureId} lectures={section.s_lectures}/>
				</Aux>
			);
		});
		let content = 	<div className="curriculam-wrapper">
							<Head/>
							<div className="curriculam-container">
								{sections}
							</div>
						</div>;

		const pathname = `/${this.props.match.params.courseId}/${this.props.currentLectureId}`;
		if(this.mounted && !(+this.props.match.params.lectureId) && (window.location.href.indexOf(pathname) === -1)){
			content = <Redirect to={pathname} />;
		}

        return(
            content
        );
    }
}

const mapStateToProps = state => {
	return {
		curriculum : state.curriculum,
		currLoaded : state.curriculumLoaded,
		currentLectureId : state.currLecId,
		currentSectionId : state.currSecId,
		subscription : state.subscription?state.subscription:null,
		isPreview: state.isPreview,
		previewEnabled: state.previewEnabled,
		previewTimer: state.previewTimer,
		courseSlug: state.course.cb_slug
	}
}

const mapDispatchToProps = dispatch => {
	return {
		setPreview: () => dispatch({ type: actionTypes.SET_PREVIEW }),
		onCurriculumSet: (curriculum) => dispatch({type :actionTypes.SET_CURRICULUM,payload : {data : curriculum}}),
		setPreviewTime: (time) => dispatch({ type: actionTypes.SET_PREVIEWTIME, payload: time }),
		setPreviewEnabled: () => dispatch({ type: actionTypes.SET_PREVIEWENABLED })
	};
}

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Curriculum));