import React from 'react';

class Single extends React.Component {
    mounted = false;

    state = {
        data : null,
        options : [],
        selected : null
    }

    componentDidMount(){
        this.mounted = true;

        if(this.mounted){
            this.setState({
                data : this.props.data,
                options : JSON.parse(this.props.data.sq_options)
            });
        }
    }

    changeOption = (selectedOption) => {
        this.setState({
            selected : selectedOption
        },() => {
            let options = JSON.parse(this.state.data.sq_options);
            let response = {
                id : this.state.data.id,
                question : this.state.data.sq_question,
                type : +this.state.data.sq_type,
                answer : options[this.state.selected]
            };
            this.props.recieveResponse(response);
        });
    }

    render(){
        let options = this.state.options.length?this.state.options:[];
        options = options.map((option,o_key) => {
            return (
                <div key={o_key} className="survey-option-row">
                    <div className="input-holder form-group clearfix">
                        <input type="radio" name={this.props.no} onChange={(e) => this.changeOption(o_key)} value={o_key} checked={o_key === this.state.selected?true:false} className="single_choice"/>
                        <label onClick={() => this.changeOption(o_key)} className="single_choice_label">{option}</label>					
                    </div>
                </div>
            );
        });
        let qClasses = ['survey-ques'];
        if(this.state.data && +this.state.data.sq_required === 1){
            qClasses.push('mandatory');
        }
        return (
            <div className="survey-row">
                <div className="survey-form-flex">
                    <div className="form-group ques-no text-right clearfix">Q{this.props.no} :</div>
                    <div className="form-group clearfix">
                        <label className={qClasses.join(' ')}>{this.state.data?this.state.data.sq_question:null}</label>
                    </div>
                    {this.props.validation?<p>{this.props.validation}</p>:null}
                </div>
                <div className="survey-option-wrapper">
                    <div>
                        {options}
                    </div>
                </div>
            </div>
        );
    }
}

export default Single;