import React,{Component} from 'react';
import { connect } from 'react-redux';
import * as actionTypes from '../../../store/actions';
import phpAxios from '../../../axios/php';
import HtmlHeader from '../document/documentHeader/DocumentHeader';
import HtmlFooter from '../document/documentFooter/DocumentFooter';
import StickyHeader from '../assignment/stickyHeader/StickyHeader';
import Checkmobile from '../../utils/services/checkmobile/Checkmobile';
import '../document/Document.css';
import { redirectToDashboard } from '../../utils/services/utils';

class Html extends Component {
    
    mounted = false;

    state = {
        opacity : 1,
        timer : null,
        content : null,
        lectureId : 0
    }

    componentWillMount(){
        this.mounted = true;

        if(this.mounted && this.props.lectureId && this.state.lectureId !== this.props.lectureId){
            this.setContent(this.props.courseId,this.props.lectureId);
        }
        this.mouseLeaveHandler();
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    setContent = (courseId,lectureId) => {
        let request = {
            header: {
                method: "lecture_detail"
            },
            body: {
                course_id : courseId,
                lecture_id : lectureId,
                lecture_type : 5
            }
        };
        phpAxios.post('/content',JSON.stringify(request))
        .then((response)=>{
            if(!response.data.header.error){
                if(this.mounted){
                    this.setState({
                        content : response.data.body.lecture.html.cl_lecture_content,
                        lectureId : this.props.lectureId
                    },() => {
                        this.props.onPercentageUpdate({
                            percentage : 100,
                            cue : 0,
                            lectureId : this.props.lectureId,
                            completed : true
                        });
                    });
                    this.refs.htmlContent.scrollTop = 0;
                }
                this.mouseEnterHandler();
                this.mouseLeaveHandler();
            }else{
                if(response.data.header.code === 604){
                    return redirectToDashboard(courseId, this.props.isPreview, this.props.courseSlug);                    
                }else{
                    //console.log(response.data.header.message);
                }
            }
        })
        .catch(err => {
            //console.log(err);
        });
    }

    mouseEnterHandler = () => {
        if (this.state.timer) {
            clearTimeout(this.state.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        if (this.mounted && !this.props.currOpen && !this.props.isPreview) {
            const timer = setTimeout(() => {
                if (this.mounted) {
                    if(Checkmobile() === false){
                    this.setState({
                        opacity: 0
                    });
                }
                }
            }, 4000);

            this.setState({
                timer: timer
            });
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.mouseEnterHandler();
            }else{
                this.mouseLeaveHandler();
            }
        }

        if(this.mounted && nextProps.lectureId && this.state.lectureId !== nextProps.lectureId){
            this.setContent(this.props.courseId,nextProps.lectureId);
        }
    }

    render(){
        const classes = this.state.opacity === 0 ? "preview-area custom-scroller full-height" : "preview-area custom-scroller";
        return(
            <div ref="htmlContent" className={classes}>
                <div className="active-topbar" onMouseEnter={this.mouseEnterHandler} onMouseLeave={this.mouseLeaveHandler}></div>
                <StickyHeader/>
                <HtmlHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity}/>
                <div className="lecture-holder">
                    <div className="context-holder document-context custom-scroller">
                        <div className="context" dangerouslySetInnerHTML={{__html: this.state.content}}>
                        </div>
                    </div>
                </div>
                <HtmlFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity}/>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        currOpen : state.currOpen,
        isPreview: state.isPreview,
        courseSlug: state.course.cb_slug
    };
}

const mapDispatchToProps = dispatch => {
    return {
		onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData})
	};
};

export default connect(mapStateToProps,mapDispatchToProps)(Html);