import React, { Component } from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';

class Transition extends Component {
    mounted = false;

    timer = null;

    state = {
        time : 4,
        currLecture : null,
        content : null,
        autoPlay : true
    }

    componentDidMount(){
        this.mounted = true;

        this.setState({
            currLecture : this.props.currLecture,
            content : this.props.transContents[Math.floor(Math.random()*this.props.transContents.length)],
            autoPlay : this.props.autoPlay
        },() => {
            this.timer = setInterval(() => {
                if(this.mounted){
                    this.setState({ 
                        time : this.state.time !== 0 ?this.state.time-1:0 
                    });
                }
            },1000);
        });
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    timerComleted = () => {
        window.clearInterval(this.timer);
        let currLecture = {...this.state.currLecture};
        if(this.mounted){
            this.setState({
                time : 4,
                currLecture : null
            },() => {
                if(typeof currLecture.nexLecture.id !== 'undefined'){
                    this.props.switchTransform();
                    const pathname = `/${this.props.courseId}/${currLecture.nexLecture.id}`;
                    this.props.history.push({
                        pathname: pathname,
                        state: { action : 'forward' }
                    });
                }else{
                    this.props.closeTransition(true);
                }
            });
        }
    }

    autoPlayToggleHandler = () => {
        this.setState({
            autoPlay : !this.state.autoPlay
        },() => {
            this.props.autoPlayToggleHandler(this.state.autoPlay);
            if(!this.state.autoPlay){
                window.clearInterval(this.timer);
            }else{
                this.timer = setInterval(() => {
                    this.setState({ 
                        time : this.state.time !== 0 ?this.state.time-1:0 
                    });
                },1000);
            }
        });
    }

    nextLectureHandler = () => {
        this.props.switchTransform();
        let currLecture = {...this.state.currLecture};
        const pathname = `/${this.props.courseId}/${currLecture.nexLecture.id}`;
        this.props.history.push({
            pathname: pathname,
            state: { action : 'forward' }
        });
    }

    componentDidUpdate(){
        if(this.state.time === 0){
            this.timerComleted();
        }
    }

    render() {
        return (
            <div className="transition-wrapper">
                <div className="transition-tag">
                    <h5>{this.state.content?this.state.content.tc_content:null}</h5>
                    <span className="transition-timer">
                        <label className="switch">
                            <input type="checkbox" onChange={() => this.autoPlayToggleHandler()} checked={this.state.autoPlay} />
                            <span className="slider round"></span>
                        </label>
                        Automatically playing in <span className="timer">0{this.state.time}</span> seconds
                    </span>
                    <div className="next-prev-navigation">
                        <div onClick={() => this.props.closeTransition(true)} className="previous"><span>Stay Here</span></div>
                        <div onClick={this.nextLectureHandler} className="next"><span>Next</span></div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        currLecture : state.currLecture?state.currLecture:null,
        transContents : state.transition,
        courseId : state.subscription.id?state.subscription.cs_course_id:0,
        isPreview: state.isPreview
    };
}

export default connect(mapStateToProps,null)(withRouter(Transition));