import * as actiontTypes from './actions';


const initialState = {
    config: {},
    currOpen : false,
    dissOpen : false,
    curriculumLoaded : false,
    course : {},
    curriculum : {},
    subscription : {},
    lectureLog : {},
    transition : [],
    pointableLectures : {},
    currLecture : null,
    currLecId : 0,
    currSecId : 0,
    opacityClass: "lecture-holder overflow-visible",
    menuOpen: false,
    showList: false,
    isPreview: false,
    previewTimer: 0,
    previewEnabled: false,
    fullScreen: false,
    token: '',
};

const reducer = (state = initialState,action) => {
    switch(action.type){
        case actiontTypes.TOGGLE_CURRICULUM : 
        return {
            ...state,
            currOpen : !state.currOpen,
            dissOpen : state.currOpen?state.dissOpen:false
        };

        case actiontTypes.TOGGLE_DISCUSSION : 
            return {
                ...state,
                dissOpen : !state.dissOpen,
                currOpen : state.dissOpen?state.currOpen:false
            };

        case actiontTypes.SET_CURRICULUM : 

        if(!state.curriculumLoaded){
            let pointableLectures = {};
            let logData = {};
            let curriculum = {...action.payload.data.curriculum};
            const urlLecture = +action.payload.data.currentLectureId;
            const Subscription = {...action.payload.data.curriculum.subscription};
            Subscription.cs_lecture_log = Subscription.cs_lecture_log?Subscription.cs_lecture_log:'{}';
            const lectureLog = JSON.parse(Subscription.cs_lecture_log);
            logData.overall_percentage = action.payload.data.curriculum.subscription.cs_percentage;
            logData.log = {...lectureLog};
            let sections = curriculum.sections.sort((a,b) => {
                return +a.s_order_no - +b.s_order_no;
            });

            let currentLecture = {};

            let currLectureId = 0;
            let currSectionId = 0;

            let lectureNumber = 0;
            let sectionNumber = 0;
            let allLectures = [];
            let surveyNumber = 0;
            let quizNumber = 0;
            let assignmentNumber = 0;
            sections = sections.map((section,s_id) => {
                let sCurrent = false;
                sectionNumber++;
                let indLectNum = 0;
                section.s_lectures = section.s_lectures.filter((lecture) => {
                    if(state.isPreview) { return !(+lecture.cl_lecture_type === 13) }
                    return !((typeof logData.log[+lecture.id] !== 'undefined') && (+lecture.cl_lecture_type === 13))
                });
                section.s_lectures = section.s_lectures.map((lecture , l_key) => {
                    let lCurrent = false;
                    lecture.sectionName = section.s_name;
                    if(+lecture.cl_lecture_type === 13){
                        surveyNumber++;
                        lecture.lNumber = surveyNumber;
                    }else if(+lecture.cl_lecture_type === 3){
                        quizNumber++;
                        indLectNum++;
                        lecture.lNumber = quizNumber;
                    }else if(+lecture.cl_lecture_type === 8){
                        assignmentNumber++;
                        indLectNum++;
                        lecture.lNumber = assignmentNumber;
                    }else{
                        lectureNumber++;
                        indLectNum++;
                        lecture.lNumber = lectureNumber;
                    }
                    lecture.sNumber = sectionNumber;
                    lecture.percentage = 0;
                    lecture.views = 0;
                    lecture.cue = 0;

                    if(lectureLog && typeof lectureLog[+lecture.id] !== 'undefined'){
                        lecture.percentage = parseInt(lectureLog[+lecture.id].percentage);
                        lecture.views = parseInt(lectureLog[+lecture.id].views);
                        lecture.cue = parseInt(lectureLog[+lecture.id].cue);
                    }

                    lecture.changes = false;

                    if(typeof currentLecture.nexLecture === 'object' && !currentLecture.nexLecture.id){
                        currentLecture.nexLecture = {...lecture};
                    }
                    
                    if((urlLecture && urlLecture === +lecture.id) || (!urlLecture && (+Subscription.cs_last_played_lecture !== 0 && +Subscription.cs_last_played_lecture === +lecture.id)) ||(!urlLecture && (s_id === 0 && l_key === 0))){
                        currentLecture = {...lecture};
                        currLectureId = +lecture.id;
                        lCurrent = true;
                        currSectionId = section.id;
                        currentLecture.prevLecture = 1;
                        currentLecture.nexLecture = {};
                    }

                    if(typeof currentLecture.prevLecture !== 'object'){
                        currentLecture.prevLecture = typeof allLectures[allLectures.length - 1] !== 'undefined'?{...allLectures[allLectures.length - 1]}:{};
                    }
                    pointableLectures[+lecture.id] = lecture;
                    allLectures.push(lecture);
                    return {
                        ...lecture,
                        current : lCurrent
                    };
                });
                return {
                    ...section,
                    open : sCurrent,
                    current : sCurrent,
                    lectureCount : indLectNum
                };
            });
            return {
                ...state,
                curriculum : [...sections],
                course : {...action.payload.data.course},
                curriculumLoaded : true,
                currLecture : currentLecture,
                currLecId : currLectureId,
                currSecId : currSectionId,
                subscription : {...Subscription},
                transition : [...action.payload.data.transition.transition_contents],
                lectureLog : {...logData},
                pointableLectures : {...pointableLectures}
            };
        }

        break;

        case actiontTypes.TOGGLE_SECTION :
            let sections = [...state.curriculum];
            const sectionId = action.payload.sectionId;
            sections = sections.map((section,s_key) => {
                section.open = (section.open === undefined) ? false : section.open;
                if(+section.id === +sectionId)
                {
                    section.open = !section.open;
                }
                return section;
            });

            return {
                ...state,
                curriculum : sections,
                currSecId : +sectionId
            }

        case actiontTypes.SWITCH_LECTURE :
            let currLectureId = 0;
            let currSectionId = 0;
            let currentLecture = {};
            let allLectures = [];
            let curriculum = Array.isArray(state.curriculum) ? state.curriculum : []; 
            let Curriculum = curriculum.map((section , s_key) => {
                section = {...section};
                section.current = false;
                section.s_lectures = section.s_lectures.filter((lecture) => {
                    return !((typeof state.lectureLog.log[+lecture.id] !== 'undefined') && (+lecture.cl_lecture_type === 13))
                });
                section.s_lectures = section.s_lectures.map((lecture , l_key) => {
                    lecture = {...lecture};
                    lecture.current = false;
                    if(typeof currentLecture.nexLecture === 'object' && !currentLecture.nexLecture.id){
                        currentLecture.nexLecture = {...lecture};
                    }

                    if(+action.payload.lectureId === +lecture.id){
                        lecture.current = true;
                        section.current = true;
                        section.open = true;
                        currLectureId = +lecture.id;
                        currSectionId = +section.id;
                        currentLecture = {...lecture};
                        currentLecture.prevLecture = 1;
                        currentLecture.nexLecture = {};
                    }
                    
                    if(typeof currentLecture.prevLecture !== 'object'){
                        currentLecture.prevLecture = typeof allLectures[allLectures.length - 1] !== 'undefined'?{...allLectures[allLectures.length - 1]}:{};
                    }

                    allLectures.push(lecture);
                    return lecture;
                });
                return section;
            });
            if(state.course.id){ 
                let resume_id = {};
                try {
                    resume_id = localStorage.getItem('resume_id');
                    resume_id = resume_id ? JSON.parse(resume_id) : {};
                    resume_id = (resume_id instanceof Object) ? resume_id : {};
                } catch(e) {
                    resume_id = {};
                }
                resume_id[`cid_${state.course.id}`] = action.payload.lectureId;
                localStorage.setItem('resume_id', JSON.stringify(resume_id)); 
            }
            return {
                ...state,
                currLecture : {...currentLecture},
                curriculum : Curriculum,
                currLecId : currLectureId,
                currSecId : currSectionId
            };

        case actiontTypes.UPDATE_PERCENTAGE:
            let updateLecture = {};
            let totalLectures = 0;
            let overallPercentage = 0;
            let logData = {...state.lectureLog};
            logData.update = logData.update?logData.update:false;
            let updateCurriculum = state.curriculum.map((section , s_key) => {
                section = {...section};
                section.s_lectures = section.s_lectures.map((lecture , l_key) => {
                    totalLectures++;
                    updateLecture = {...lecture};
                    if(+action.payload.lectureId === +lecture.id){
                        updateLecture.percentage = action.payload.percentage;
                        updateLecture.cue = action.payload.cue;
                        updateLecture.update = false;
                        updateLecture.views = typeof updateLecture.views !== 'undefined'?updateLecture.views:0;
                        updateLecture.percentage = typeof updateLecture.percentage !== 'undefined'?updateLecture.percentage:0;
                        if(action.payload.completed){
                            updateLecture.percentage = 0;
                            updateLecture.views += 1;
                            updateLecture.cue = 0;
                            updateLecture.update = true;
                        }
                        if(typeof logData.log[+updateLecture.id] === 'undefined'){
                            logData.log[+updateLecture.id] = {};
                        }
                        const views = ((+updateLecture.cl_limited_access > 0) && (updateLecture.views > +updateLecture.cl_limited_access)) ? +updateLecture.cl_limited_access : updateLecture.views;
                        logData.log[+updateLecture.id].percentage = updateLecture.percentage;
                        logData.log[+updateLecture.id].views = views;
                        logData.log[+updateLecture.id].nviews = (updateLecture.views - 1);
                        logData.log[+updateLecture.id].cue = updateLecture.cue;
                        logData.log[+updateLecture.id].update = updateLecture.update;
                        updateLecture.changes = true;
                    }

                    if(updateLecture.views>0){
                        overallPercentage += 100;
                    }else{
                        const pc = parseInt(updateLecture.percentage);
                        overallPercentage += isNaN(pc) ? 0 : pc;
                    }
                    
                    return {...updateLecture};
                });
                return {...section};
            });

            overallPercentage = overallPercentage/totalLectures;
            logData.overall_percentage = overallPercentage;
            logData.update = logData.update?logData.update:action.payload.completed;
            return {
                ...state,
                curriculum : [...updateCurriculum],
                lectureLog : {...logData},
            }
        case actiontTypes.UPDATE_COMPLETE:
            logData = {...state.lectureLog};
            logData.update = false;
            for (let key in logData.log) {
                logData.log[key].update = false;
            }
            return {
                ...state,
                lectureLog : {...logData}
            }
        case actiontTypes.SET_OPACITY_CLASS:
            return { ...state, opacityClass: action.payload }
            
        case actiontTypes.SET_MENU_OPEN:
            return { ...state, menuOpen: action.payload }  

        case actiontTypes.SHOW_LIST:
            return { ...state, showList: !state.showList }

        case actiontTypes.SET_CONFIG:
            return { ...state, config: action.payload }

        case actiontTypes.SET_PREVIEW:
            return { ...state, isPreview: true }

        case actiontTypes.SET_PREVIEWTIME:
            const previewTimer = action.payload ? action.payload : (--state.previewTimer);
            return { ...state, previewTimer }

        case actiontTypes.SET_PREVIEWENABLED:
            return { ...state, previewEnabled: true }

        case actiontTypes.SET_FULLSCREEN:
            return { ...state, fullScreen: !state.fullScreen }

        case actiontTypes.SET_TOKEN:
            return { ...state, token: action.payload }

        default:

        return state;
    }
}

export default reducer;