import React,{Component} from 'react';
import {connect} from 'react-redux';
import * as actionTypes from '../../../store/actions';

class Head extends Component {
    render(){
        return(
            <div className="curriculam-head">
                <span className="curriculam-head-title">Course curriculum</span>
                <span onClick={this.props.onCurriculumToggle} className="close-icon"></span>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
	return {
		onCurriculumToggle: () => dispatch({type :actionTypes.TOGGLE_CURRICULUM})
	};
}

export default connect(null,mapDispatchToProps)(Head);