import React, {Component} from 'react';
import {connect} from 'react-redux';
import * as actionTypes from '../../../store/actions';
import VideoHeader from '../video/videoHeader/VideoHeader';
import VideoFooter from '../video/videoFooter/VideoFooter';
import Plyr from 'react-plyr';
import {getPreviewDetails} from '../../utils/services/utils';
import '../plyr/Plyr.css';
import './Mp3.css';

class Mp3 extends Component {

    mounted = false;
    timer = null;

    state = {
        timer : 0,
        cue : 0,
        percentage : 0,
        currLecture : null,
        swipeEvent : false,
        opacity: 1
    }
    
    componentDidMount() {
        this.mounted = true;

        if(this.mounted && this.props.currLecture){
            this.setState({
                currLecture : {...this.props.currLecture}
            });
        }
        getPreviewDetails(this.props.courseId, this.props.isPreview, this.props.courseSlug).catch(console.log)
    }

    componentWillUnmount(){
        this.mounted = false;
        this.onPercentageUpdate = null;
    }

    componentDidUpdate(){
        if(this.props.currLecture && typeof this.props.currLecture.id !== 'undefined' && typeof this.state.currLecture.id !== 'undefined' && +this.props.currLecture.id !== +this.state.currLecture.id && this.props.audioId){
            this.setState({
                currLecture : {...this.props.currLecture}
            });
        }
    }

    onPercentageUpdate = (data) =>{
        let prevTimer = this.state.timer;
        if(data.now%5 !== 0 && !data.completed){
            let percentage = (data.now/data.duration)*100;
            this.setState({
                timer : Math.ceil(data.now),
                cue : data.now,
                percentage : percentage
            },() => {
                if(prevTimer !== this.state.timer){
                    this.props.onPercentageUpdate({
                        percentage : this.state.percentage,
                        cue : this.state.cue,
                        lectureId : this.state.currLecture.id,
                        completed : data.completed
                    });
                }
            });
        }

        if(data.completed){
            this.props.onPercentageUpdate({
                percentage : data.percentage,
                cue : 0,
                lectureId : this.state.currLecture.id,
                completed : data.completed
            });
            this.props.onComplete();
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.setState({
                    swipeEvent : nextProps.swipeEvent
                });
            }
        }
    }

    mouseEnterHandler = () => {
        if (this.timer) {
            clearTimeout(this.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        // if (this.mounted && !this.props.currOpen) {
        //     this.timer = setTimeout(() => {
        //         if (this.mounted) {
        //             this.setState({
        //                 opacity: 0
        //             });
        //         }
        //     }, 4000);
        // }
    }

    render(){
        let audioSource = this.props.config.mp3Url.replace("{audioId}", this.props.audioId);
        audioSource = audioSource.replace("{courseId}", this.props.courseId);
        let Player = null;

        if(this.state.currLecture){
            Player = <Plyr 
                        type="audio" 
                        url={audioSource}
                        className="audio-player"
                        seekTime={600}
                        autoplay={this.props.autoPlay}
                        onEnd={() => {this.onPercentageUpdate({percentage:100,completed:true})}}
                        // onReady={(data) => {console.log(data)}}
                        onTimeUpdate={(data) => this.onPercentageUpdate({...data,completed:false})}
                        // onPlay={(data) => {console.log(data)}}
                        controls={['mute','progress','duration','play']} />
        }

        return(
            <div className="lecture-content" style={{backgroundColor:'#000'}}>
                <div className="mp3-header"><VideoHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent}/></div>
                <div className="plyr-audio">
                    {Player}
                </div>
                <VideoFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent} />
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
		onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData})
	};
};

const mapStateToProps = state => {
    return {
        config: state.config,
        currLecture : state.currLecture,
        isPreview: state.isPreview,
		courseSlug: state.course.cb_slug
    }
};

export default connect(mapStateToProps,mapDispatchToProps)(Mp3);