import React,{Component} from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';
import * as actionTypes from '../../../../store/actions';
import SupportDoc from '../SupportDoc';
import { toHHMMSS, fullScreen, exitFullScreen } from '../../../utils/services/utils';
import expandIcon from '../../../../assets/images/expand.svg'
import minimizeIcon from '../../../../assets/images/minimize.svg'

class LectureFooter extends Component {

    mounted = false;

    state = {
        currentLecture : null,
    }

    _handleFullscreenChange = (event) => {
        let elem = event.target;
        let isFullscreen = document.fullscreenElement === elem;
        if((isFullscreen && !this.props.fullScreen) || (!isFullscreen && this.props.fullScreen)) {
            this.props.setFullScreen();
        }
    }

    componentDidMount() {
        this.mounted = true;

        /* Standard syntax */
        document.addEventListener("fullscreenchange", this._handleFullscreenChange);
        /* Firefox */
        document.addEventListener("mozfullscreenchange", this._handleFullscreenChange);
        /* Chrome, Safari and Opera */
        document.addEventListener("webkitfullscreenchange", this._handleFullscreenChange);
        /* IE / Edge */
        document.addEventListener("msfullscreenchange", this._handleFullscreenChange);

        if(this.mounted && !this.state.currentLecture && this.props.currentLecture){
            this.setState({
                currentLecture : this.props.currentLecture
            });
        }
    }

    componentDidUpdate(prevProps){
        this.mounted = true;

        // if(this.mounted && this.state.currLectureId && this.state.currentLecture && +this.state.currentLecture.id !== +this.props.currentLecture.id){
        //     this.setState({
        //         currentLecture : this.props.currentLecture
        //     });
        // }

        if(this.mounted && prevProps.currentLecture){
            if(this.props.currentLecture.id !== prevProps.currentLecture.id) {
                this.setState({
                    currentLecture : this.props.currentLecture
                });
            }
        }
    }

    componentWillUnmount() {
        this.mounted = false;
        /* Standard syntax */
        document.removeEventListener("fullscreenchange", this._handleFullscreenChange);
        /* Firefox */
        document.removeEventListener("mozfullscreenchange", this._handleFullscreenChange);
        /* Chrome, Safari and Opera */
        document.removeEventListener("webkitfullscreenchange", this._handleFullscreenChange);
        /* IE / Edge */
        document.removeEventListener("msfullscreenchange", this._handleFullscreenChange);
    }

    lectureSwitch = (params) => {
        const pathname = `/${this.props.match.params.courseId}/${params.lectureId}`;
        this.props.history.push({
            pathname: pathname,
            state: { action : params.direction }
        })
    }

    discussionToggleHandler = () => {
        this.props.onDiscussionToggle();
    }

    _handleClick = () => {
        this.props.onShowList();
        if(this.props.currOpen) {
            this.props.onCurriculumToggle()
        }
    }

    _toggleFullScreen = () => {
        if(this.props.fullScreen) {
            exitFullScreen();
        } else {
            fullScreen();
        }
        this.props.setFullScreen();
    }

    _showSupportDocButton = () => {
        let supportFiles = this.props.currentLecture.cl_support_files;
        supportFiles = supportFiles ? JSON.parse(supportFiles) : {};
        delete supportFiles[""];
        const filesArray = Object.values(supportFiles);

        if(!this.props.isPreview && (filesArray.length > 0)) {
            return (
                <div className="footer-left-col toggle-docs" onClick={this._handleClick} title="Support Files">
                    <span className="download-icon">
                        <svg width="22" height="22" viewBox="0 0 24 24" fill="#595959" xmlns="http://www.w3.org/2000/svg">
                            <g>
                            <rect fill="none" id="canvas_background" height="402" width="582" y="-1" x="-1"/>
                            </g>
                            <g>
                            <path id="svg_1" d="m11,14.59l0,-11.59a1,1 0 0 1 2,0l0,11.59l3.3,-3.3a1,1 0 0 1 1.4,1.42l-5,5a1,1 0 0 1 -1.4,0l-5,-5a1,1 0 0 1 1.4,-1.42l3.3,3.3zm-8,2.41a1,1 0 0 1 2,0l0,3l14,0l0,-3a1,1 0 0 1 2,0l0,3a2,2 0 0 1 -2,2l-14,0a2,2 0 0 1 -2,-2l0,-3z" className="heroicon-ui"/>
                            </g>
                        </svg>
                    </span>
                </div>
            )
        }

        return <div className="footer-left-col toggle-docs"></div>
    }

    render(){

        let nextLecture = null;
        let prevLecture = null;

        if(this.state.currentLecture && this.state.currentLecture.nexLecture && typeof this.state.currentLecture.nexLecture.id !== 'undefined'){
            nextLecture = this.state.currentLecture.nexLecture;
        }

        if(this.state.currentLecture && typeof this.state.currentLecture.prevLecture.id !== 'undefined'){
            prevLecture = this.state.currentLecture.prevLecture;
        }
        

        return(
            <>
                <SupportDoc />
                <div className="lecture-footer-container" onMouseEnter={this.props.mouseEnterHandler} onMouseLeave={this.props.mouseLeaveHandler} >
                    <div className="lecture-footer" style={{opacity:this.props.opacity,visibility:this.props.opacity?'visible':'hidden'}}>
                        <div>
                            {this._showSupportDocButton()}
                            {this.props.isPreview && <div title="Preview time" className="time-ramining align-items-center"><span>Time left:</span><span className="time">&nbsp;{toHHMMSS(this.props.previewTimer)}</span></div>}
                        </div>
                        
                        <div className="footer-right-col">
                            {prevLecture?<div title="Previous Lecture" onClick={() => {this.lectureSwitch({direction:'backward',lectureId:prevLecture.id})}} className="prev-lecture"><span>Prev</span></div>:prevLecture}
                            {nextLecture?<div title="Next Lecture" onClick={() => {this.lectureSwitch({direction:'forward',lectureId:nextLecture.id})}} className="next-lecture"><span>Next</span></div>:nextLecture}
                            {!this.props.isPreview && 
                                <div title="Ask a question">							
                                    <span className="ask-holder" onClick={this.discussionToggleHandler}><span className="ask">Ask</span> <span className="ask-icon">?</span></span>
                                </div>
                            }
                            <div className="screen-toggle" onClick={this._toggleFullScreen}>
                                <img className="expand-icon" src={this.props.fullScreen ? minimizeIcon : expandIcon} alt="Toggle fullscreen" title="Toggle fullscreen" />
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}

const mapStateToProps = state => {
    return {
        currentLecture : state.currLecture,
        currOpen : state.currOpen?state.currOpen:false,
        isPreview: state.isPreview,
        previewTimer: state.previewTimer,
        fullScreen: state.fullScreen
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onDiscussionToggle: () => dispatch({type :actionTypes.TOGGLE_DISCUSSION}),
        onShowList: () => dispatch({ type: actionTypes.SHOW_LIST }),
        onCurriculumToggle: () => dispatch({ type: actionTypes.TOGGLE_CURRICULUM }),
        setFullScreen: () => dispatch({ type: actionTypes.SET_FULLSCREEN }),
    }
}


export default connect(mapStateToProps,mapDispatchToProps)(withRouter(LectureFooter));