import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import Axios from '../../../axios/php';
import * as actionTypes from '../../../store/actions';
import Aux from '../../../hoc/Aux/Aux';
import '../../../assets/css/redactor.css';
import './Assignment.css';
import DocumentHeader from '../document/documentHeader/DocumentHeader';
import StickyHeader from '../assignment/stickyHeader/StickyHeader';
import DocumentFooter from '../document/documentFooter/DocumentFooter';
import AttachmentIcon from '../../utils/icons/attachment/Attachment';
import { redirectToDashboard } from '../../utils/services/utils';

const ALLOWEDEXT = ['doc','docx','pdf','xls','xlsx','ppt','pptx'];

const showUploadMessage = (message, textClass = 'text-danger') => {
    // remove
    var item = document.getElementById("redactor_upload_status");
    if(item){ item.parentNode.removeChild(item); }
    // add
    var divEl = document.createElement("DIV");                       // Create a <p> node
    var t = document.createTextNode(message);
    divEl.appendChild(t);
    divEl.classList.add('text-center', 'file-types', textClass);
    divEl.setAttribute("id", "redactor_upload_status");
    document.querySelector('.redactor-modal-body').appendChild(divEl);
}

const hideUploadMessage = () => {
    var item = document.getElementById("redactor_upload_status");
    if(item){ item.parentNode.removeChild(item); }
}

class Assignment extends Component {
    mounted = false;

    state = {
        activeTab: 0,
        opacity: 1,
        timer: null,
        courseId: 0,
        course : null,
        subscription : null,
        currLecture : null,
        lectureId: 0,
        assignmentData: null,
        showSuccess : null,
        submisionData: {
            files: [],
            description: null
        },
        submitting : false,
        assignMentLoading : true,
        readMore : true
    }

    componentDidMount() {
        this.mouseLeaveHandler();
        this.mounted = true;
        let tab = this.props.match.params.tab ? parseInt(this.props.match.params.tab) : 0;
        tab = isNaN(tab) ? 0 : tab;
        tab = ([0, 1, 2].indexOf(tab) > -1) ? tab : 0;
        if (this.mounted) {
            this.setState({
                courseId: this.props.courseId,
                lectureId: this.props.lectureId,
                course : this.props.course,
                currLecture : this.props.currLecture,
                subscription : this.props.subscription,
                activeTab: tab
            }, () => {
                if(this.props.lectureId){ this.loadAssignmentData(); }
            });
            this.mouseLeaveHandler();
        }
    }

    loadAssignmentData = () => {
        const request = {
            header: {
                service: "",
                method: "assignment"
            },
            body: {
                course_id: this.props.match.params.courseId,
                lecture_id: this.props.lectureId
            }
        };
        if(this.mounted){
            this.setState({
                assignMentLoading : true
            });
        }
        Axios.post(`/content`, JSON.stringify(request))
            .then(response => {
                if (!response.data.header.error) {
                    if (this.mounted) {
                        this.setState({
                            assignmentData: { ...response.data.body },
                            assignMentLoading : false,
                            submisionData: {
                                files: [],
                                description: null
                            },
                            submitting : false
                        });
                    }
                } else {
                    if(response.data.header.code === 604){
                        return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
                    }else{
                        console.log(response.data.header.message);
                    }
                }
            })
            .catch(err => {
                //console.log(err);
            });
    }

    componentDidUpdate(prevProps,prevState){
        if (this.mounted && this.props.currLecture && ((+prevProps.currLecture.id !== +this.props.currLecture.id) || (prevProps.lectureId !== this.props.lectureId))) {
            this.setState({
                lectureId: this.props.lectureId,
                currLecture : this.props.currLecture,
                activeTab : 0,
                assignmentData : null,
                readMore : true
            }, () => {
                this.loadAssignmentData();
            });
        }
    }

    componentWillUnmount() {
        this.mounted = false;
    }

    downloadHandler = (attachment) => {
        let link = this.props.config.materialUrl.replace("{courseId}", this.state.courseId);
        link = link.replace("{purpose}", attachment.source);
        switch (attachment.source) {
            case 'assignment':
                link = link.replace("{file}", attachment.file_name);
                break;
            case 'assignment_submission':
                link = link.replace("{file}", attachment.encrypted);
                break;
            default:
        }
        window.open(link, '_blank');
    }

    tabSwitchHandler = (tab) => {
        if (this.mounted) {
            this.setState({
                activeTab: tab
            });
        }
    }

    mouseEnterHandler = () => {
        if (this.state.timer) {
            clearTimeout(this.state.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        // if (this.mounted && !this.props.currOpen) {
        //     const timer = setTimeout(() => {
        //         if (this.mounted) {
        //             this.setState({
        //                 opacity: 0
        //             });
        //         }
        //     }, 4000);

        //     this.setState({
        //         timer: timer
        //     });
        // }
    }

    componentWillReceiveProps(nextProps){
        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.mouseEnterHandler();
            }else{
                this.mouseLeaveHandler();
            }
        }
    }

    redactorChangedHandler = (content = false) => {
        const description = content ? content : window.$R('#submision_editor', 'source.getCode').trim();
        let submisionData = { ...this.state.submisionData }
        submisionData.description = description;
        this.setState({
            submisionData: { ...submisionData }
        });
    }

    submitHandler = () => {
        let submissionData = {
            course_id: this.state.courseId,
            lecture_id: this.state.lectureId,
            subscribe_id: this.state.subscription.id,
            assignment_name: this.state.currLecture.cl_lecture_name,
            course_name: this.state.course.cb_title,
            files: [...this.state.submisionData.files],
            comments: this.state.submisionData.description
        };

        const request = {
            header: {
                service: "",
                method: "assignment_submission"
            },
            body: submissionData
        };
        this.refs.fileTarget.innerHTML = '';
        this.setState({
            submitting : true
        });
        Axios.post(`/content`, JSON.stringify(request))
            .then(response => {
                if (!response.data.header.error) {
                    if (this.mounted) {
                        this.loadAssignmentData();
                        this.props.onPercentageUpdate({
                            percentage : 100,
                            cue : 0,
                            lectureId : this.state.lectureId,
                            completed : true
                        });

                        this.setState({
                            showSuccess : true
                        });

                        setTimeout(() => {
                            if(this.mounted){
                                this.setState({
                                    showSuccess : false
                                });
                            }
                        },2000);
                    }
                } else {
                    //console.log(response.data.header.message);
                }
                this.setState({
                    submitting : false
                });
            })
            .catch(err => {
                //console.log(err);
            });
    }

    deleteFile(file){
        const request = {
            header: {
                service: "",
                method: "remove_assignment_file"
            },
            body: {
                file : file
            }
        };
        Axios.post(`/content`, JSON.stringify(request))
            .then(res => {
                let files = [...this.state.submisionData.files];
                files.splice(files.findIndex(function(fileItem){
                    return fileItem.encrypted === file;
                }), 1);
                this.setState({submisionData:{
                    ...this.state.submisionData,
                    files : [...files]
                }});
                return true;
            }).catch(err => {
                //console.log(err);
                return false;
            });
    }

    readMore = () => {
        if(this.mounted){
            this.setState({
                readMore : false
            });
        }
    }

    render() {
        let classes = [];
        classes[0] = ['tab', 'tablinks'];
        classes[1] = ['tab', 'tablinks'];
        classes[2] = ['tab', 'tablinks'];
        classes[this.state.activeTab].push('active');

        let content = null;
        const assignMent = this.state.assignmentData;
        const submittable = this.state.submisionData.files.length;

        if (assignMent && this.state.currLecture) {
            assignMent.a_data.dt_uploded_files = assignMent.a_data.dt_uploded_files ? assignMent.a_data.dt_uploded_files : '';
            let assignmentAttach = assignMent.a_data.dt_uploded_files!== ''?JSON.parse(assignMent.a_data.dt_uploded_files):[];
            
            let assignmentAttachments = [];
            for (let [key, value] of Object.entries(assignmentAttach)) {
                assignmentAttachments.push(<AttachmentIcon idNum={key} downloadHandler={this.downloadHandler} downloadData={{ ...value, source: 'assignment' }} key={value.file_name} fileName={value.name} />)
            }

            switch (this.state.activeTab) {
                case 1:
                    content = <Aux>
                                <div id="submision" className="tabcontent context custom-scroller">
                                    
                                    <div className="text-center">
                                        <ul className="instruction-list">
                                            <li>Upload atleast one file from the upload icon in the editor.</li>
                                            <li>Add description if needed.</li>
                                            <li>Once submitted, cannot be edited.</li>
                                            <li className="file-types">Supported files : .doc,.docx,.pdf,.xls,.xlsx,.ppt and .pptx</li>
                                        </ul>
                                    </div>

                                    <textarea style={{ visibility: "hidden" }} value={this.state.submisionData.description ? this.state.submisionData.description : ''} onChange={() => { }} id="submision_editor" className="form-control"></textarea>
                                    <div ref="fileTarget" id="file-target" className="attachment-holder">
                                        
                                    </div>

                                    <div className="text-center btn-container">
                                        <button className="btn-violet" onClick={submittable?this.submitHandler:null} style={{cursor:submittable?'pointer':'not-allowed'}} type="">{this.state.submitting?'Submitting...':'Submit'}</button>
                                    </div>

                                </div>
                                
                            </Aux>
                break;
                case 2:
                    let commentHtml = '';
                    if (assignMent.submitted) {
                        let submittedData = JSON.parse(assignMent.submission.dtua_comments);
                        if(submittedData.length > 1){
                            let comments = [...submittedData];
                            comments = comments.filter(function(value, index, arr){
                                return index !== 0;
                            });
                            comments = comments.map((comment,c_key) => {
                                return (
                                    <div key={c_key} className="faculty-feedback">
                                        <h4>Comment {c_key+1}</h4>
                                        <p>{comment.comment}</p>
                                    </div>
                                );
                            });
                            // console.log('Submitted Data : ',submittedData);
                            commentHtml =   <div className="feedback-container">
                                                <div className="text-center">
                                                    <span className="faculty-title">Faculty Feedback</span>
                                                    {comments}
                                                </div>
                                            </div>
                        }
                    }

                    content =   <div id="result" className="tabcontent context custom-scroller">
                                    {(assignMent.submitted && +assignMent.submission.dtua_evaluated)?'':<span className="result-notice">Awaiting for result</span>}
                                    <div className="result-wrapper">
                                        <div className="result-info">
                                            <span className="result-info-title">Submitted On</span>
                                            <span className="submition-date">{assignMent.submitted?assignMent.submission.created_date:' - '}</span>
                                        </div>
                                        <div className="result-info">
                                            <span className="marks">{(assignMent.submitted && +assignMent.submission.dtua_evaluated)?assignMent.submission.mark:' - '}</span>
                                            <span className="result-info-title">Marks Scored</span>
                                        </div>
                                        <div className="result-info">
                                            <span className="grade">{(assignMent.submitted && +assignMent.submission.dtua_evaluated)?assignMent.submission.dtua_grade:' - '}</span>
                                            <span className="result-info-title">Grade</span>
                                        </div>
                                    </div>
                                    {commentHtml}
                                </div>;
                    break;
                default:
                    let classes = ['tabcontent','context','custom-scroller']
                    if(this.state.readMore){
                        classes.push('content-read-toggle');
                    }
                    let buttonText = 'Continue';
                    if(assignMent.submitted) { buttonText = 'Submitted' }
                    else if(assignMent.expired) { buttonText = 'Expired' }
                    content = <Aux>
                                <div id="instruction" className={classes.join(' ')}>
                                    <span className="instruction-info">{(assignMent.expired && !assignMent.submitted)?'Expired on - ':'Submit Before - '} 
                                                    <span className="submision-date">{assignMent.a_data.dt_last_date}</span>
                                    </span>
                                    
                                    <div dangerouslySetInnerHTML={{ __html: this.state.currLecture.cl_lecture_content }}></div>
                                    <div ref="fileTarget" id="file-target" className="hidden attachment-holder"></div>
                                    <div className="attachment-holder">
                                        {assignmentAttachments}
                                    </div>
                                    {this.state.readMore?<div onClick={this.readMore} className="read-more">Read More</div>:null}
                                    
                                </div>
                                <div className="text-center btn-container">
                                    <button onClick={() => (!assignMent.expired || assignMent.submitted)?this.tabSwitchHandler(1):null} style={{cursor:(!assignMent.expired || assignMent.submitted)?'pointer':'not-allowed'}} className="btn-violet" type="">{buttonText}</button>
                                </div>
                            </Aux>
                
            }

            if (this.state.activeTab === 1 && !assignMent.submitted) {
                let fileUrl = this.props.config.redactorUploadUrl.replace('{courseId}', this.state.courseId);
                fileUrl = fileUrl.replace('{purpose}', 'assignment_submission');
                setTimeout(() => {
                    window.$R('#submision_editor', {
                        minHeight: '200px',
                        maxHeight: '200px',
                        toolbarFixed: false,
                        styles: true,
                        shortcuts: true,
                        callbacks: {
                            file: {
                                uploaded: (file, response) => {
                                    let submisionData = { ...this.state.submisionData };
                                    submisionData.files.push({
                                        file: response['filekey-0'].name,
                                        encrypted: response['filekey-0'].file_name
                                    });

                                    this.setState({
                                        submisionData: { ...submisionData }
                                    })
                                },
                                delete: (file) => {
                                    file = file.nodes[0].dataset.file;
                                    return this.deleteFile(file);
                                }
                            },
                            upload: {
                                beforeSend: function(xhr) {
                                    try {
                                        showUploadMessage('Uploading...', 'text-success');
                                        var file = document.querySelector('.redactor-modal-tab input[type=file]').files[0];
                                        var fileSplit = file.name.split('.');
                                        var ext = fileSplit.pop();
                                        if (ALLOWEDEXT.indexOf(ext) === -1) {
                                            showUploadMessage('Invalid file extension!');
                                            this.progress.hide();
                                            return false;
                                        } else if(file.size > 1e+7) {
                                            showUploadMessage('File size must be < 10 MB!');
                                            this.progress.hide();
                                            return false;
                                        }
                                    } catch (e) {}
                                },
                                complete: function(response) {
                                    hideUploadMessage();
                                },
                                error: function(response) {
                                    hideUploadMessage();
                                }
                            },
                            keyup: (e) => {
                                this.redactorChangedHandler(e.target.innerHTML);
                            },
                            pasted: () => {
                                this.redactorChangedHandler();
                            }
                        },
                        fileUpload: fileUrl,
                        fileAttachment: '#file-target'
                    });
                }, 100);
            } else {
                window.$R('#submision_editor', 'destroy');

                if (this.state.activeTab === 1 && assignMent.submitted) {
                    let submittedData = JSON.parse(assignMent.submission.dtua_comments);
                    let files = JSON.parse(submittedData[0].file);
                    files = files.map((file,f_key) => {
                        return <AttachmentIcon downloadHandler={this.downloadHandler} downloadData={{ ...file, source: 'assignment_submission' }} key={file.encrypted} fileName={file.file} />;
                    });
                    // console.log('Submitted Data : ',submittedData);
                    content = <div id="submision" className="tabcontent context custom-scroller">
                        <div dangerouslySetInnerHTML={{__html: submittedData[0].comment}}></div>
                        <div className="attachment-holder">
                            {files}
                        </div>
                    </div>
                }
            }

            if (this.state.activeTab === 1 && assignMent.expired && !assignMent.submitted) {
                content = <div id="submision" className="tabcontent context custom-scroller">
                    <span className="result-notice">Sorry Submission is not allowed as it is expired.</span>
                </div>
            }
        }

        if(this.state.showSuccess){
            content = <span className="result-success">Submitted successfully.</span>
        }

        return (
            <div className="preview-area custom-scroller">
                
                <StickyHeader/>
                
                <DocumentHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
                <div className="lecture-holder text-center">
                <div><h2>Assignment</h2></div>
                    <div className="content-toggle-tab" style={{display:'inline-block'}}>
                        <div className={classes[0].join(' ')} onClick={() => this.tabSwitchHandler(0)} id="defaultOpen">Instructions</div>
                        <div className={classes[1].join(' ')} onClick={() => (assignMent&&(assignMent.submitted || !assignMent.expired))?this.tabSwitchHandler(1):null} style={{cursor:(assignMent&&(assignMent.submitted || !assignMent.expired))?'pointer':'not-allowed'}}>Submission</div>
                        <div className={classes[2].join(' ')} style={{ cursor: (assignMent && !assignMent.submitted) ? 'not-allowed' : 'pointer' }} onClick={() => (assignMent && !assignMent.submitted) ? null : this.tabSwitchHandler(2)}>Result</div>
                    </div>
                    <div className="context-holder">
                        {this.state.assignMentLoading?null:content}
                    </div>
                </div>
                <DocumentFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        config: state.config,
        courseId: state.subscription.id ? state.subscription.cs_course_id : 0,
        lectureId: state.currLecId,
        currOpen: state.currOpen,
        subscription: state.subscription,
        course : state.course,
        currLecture : state.currLecture,
        isPreview: state.isPreview,
        courseSlug: state.course.cb_slug
    };
}

const mapDispatchToProps = dispatch => {
    return {
		onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData})
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Assignment));