import React, { Component } from 'react';
import { HashRouter } from 'react-router-dom';
import Content from './containers/Content';
import axios from 'axios';
import { connect } from 'react-redux';
import {SET_CONFIG,SET_TOKEN} from './store/actions';
import ErrorBoundary from './containers/ErrorBoundary';
import { BASEURL } from './components/utils/services/utils';

const responseKeys = ["apiUrl", "assessmentUrl", "content_secuirity", "documentUrl", "materialUrl", "mp3Url", "redactorUploadUrl", "scormUrl", "siteName", "user_details"];

const urlParams = new URLSearchParams(window.location.search);
const token = urlParams.get('token');

let axiosOptions = {
  baseURL : BASEURL,
  crossDomain: true
};

if(token) {
  localStorage.setItem('token', token);
  axiosOptions.headers = {'x-api-key': token};
}

const Axios = axios.create(axiosOptions);

const validateResponse = (keys) => {
  keys = keys.sort();
  if(responseKeys.toString() === keys.toString()) {
    return true;
  }
  return false;
}

class App extends Component {
  state = { message: 'Loading...' }

  componentDidMount() {
    if(token) {
      this.props.saveToken(token)
    }

    const request = {
      header: {
          service: "",
          method: "content_config",
      },
      body: {}
    };
    Axios.post(`/content`, JSON.stringify(request))
      .then(response => {
        if (!response.data.header.error) {
          if(!validateResponse(Object.keys(response.data.body))) { 
            throw new Error("Invalid site configuration!") 
          }
          this.props.saveConfigs(response.data.body);
        } else {
          this.setState({ message: response.data.header.message });
          // window.location = BASEURL + '/dashboard'
          return "AndroidInterface.finish()";
        }
      })
      .catch(err => {
        console.log(err);
        this.setState({ message: err.message });
        // window.location = BASEURL + '/dashboard'
        return "AndroidInterface.finish()";
      });
  }

  render() {
    if(this.props.config.apiUrl) {
      return (
        <HashRouter>
          <ErrorBoundary>
            <Content />
          </ErrorBoundary>
        </HashRouter>
      );
    } 
    
    return <div className="root-load">{this.state.message}</div>
  }
}

const mapStateToProps = state => {
  return {
    config: state.config
  }
}

const mapDispatchToProps = dispatch => {
  return {
      saveConfigs: (configs) => dispatch({ type: SET_CONFIG, payload: configs }),
      saveToken: (token) => dispatch({ type: SET_TOKEN, payload: token }),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);