import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actionTypes from '../../../store/actions';
import VideoHeader from './videoHeader/VideoHeader';
import VideoFooter from './videoFooter/VideoFooter';
import {BASEURL, HOST, getPreviewDetails} from '../../utils/services/utils';

const flowplayer = window.flowplayer;
const vidUrl = `${BASEURL}/uploads/${HOST}/course/{courseId}/videos/`;
class Flow extends Component {

  mounted = false;
  player = false;
  videoProgress = {};
  timer = null;

  state = {
    timer: 0,
    cue: 0,
    percentage: 0,
    swipeEvent: false,
    errored: false,
    opacity: 1
  }

  constructor(props) {
    super(props);
    window.TGN.fetchTokens();
  }

  onReady = (ev, api) => {
    api.seek(this.state.cue);
    api.volume(0.5);
    // if (this.props.dissOpen || this.props.currOpen) {
    //   api.pause();
    // }
    if (this.props.autoPlay) {
      api.play();
    }
  }

  onTimeUpdate = (e, api) => {
    const currentPos = api.ready ? api.video.time : 0;
    const duration = api.ready ? api.video.duration : 0;
    const percentage = (currentPos / duration);

    this.onPercentageUpdate({
      seconds: currentPos,
      percent: percentage,
      completed: false
    })
  }

  onEnded = (e, api) => {
    // const currentPos = api.ready ? api.video.time : 0;
    const duration = api.ready ? api.video.duration : 0;
    const percentage = 1;

    this.onPercentageUpdate({
      seconds: duration,
      percent: percentage,
      completed: true
    })
  }

  onPaused = (e, api) => {
    const currentPos = api.ready ? api.video.time : 0;
    this.videoProgress[`${this.props.courseId}_${this.props.currLecture.id}`] = currentPos;
    localStorage.setItem('video_progress', JSON.stringify(this.videoProgress))
  }

  onError = (e, api) => {
    this.player.shutdown();
    this.setState({ errored: true });
  }

  onShutdown = (e, api) => {
    api.engine.hls.stopLoad();
    console.log('shutdown ',api)
  }

  componentDidMount() {
    try {
      this.mounted = true;
      this.videoProgress = localStorage.getItem('video_progress');
      this.videoProgress = this.videoProgress ? JSON.parse(this.videoProgress) : {};

      if (this.mounted && this.props.currLecture) {
        this.initPlayer();
        this.setState({
          cue: (this.videoProgress[`${this.props.courseId}_${this.props.currLecture.id}`] || this.props.currLecture.cue)
        });

        this.player.one('ready', this.onReady)
        this.player.on("progress", this.onTimeUpdate);
        this.player.on("finish", this.onEnded);
        this.player.on("pause", this.onPaused);
        this.player.on("error", this.onError);
        this.player.on("shutdown", this.onShutdown);
        this.mouseLeaveHandler();
      }
      getPreviewDetails(this.props.courseId, this.props.isPreview, this.props.courseSlug).catch(console.log)
    } catch (e) { }
  }

  initPlayer() {
    const vidPath = vidUrl.replace('{courseId}', this.props.courseId);
    const vidSrc = `${vidPath}${this.props.videoId}`;
    flowplayer(function (api, root) {
      var fsbutton = root.querySelector(".fp-fullscreen");
     
      // append fullscreen button after HD menu is added on ready
      api.on("ready", function () {
        root.querySelector(".fp-controls").appendChild(fsbutton);
      });
    });
    this.player = flowplayer("#player", {
      embed: false,
      hlsQualities: true,
      seekable: true,
      clip: {
        sources: [
          { type: "application/x-mpegurl", src: vidSrc }
        ],
        qualities: ['240p', '360p', '480p', '720p', '1080p'],
        defaultQuality: '480p'
      }
    })
  }

  componentDidUpdate(prevProps) {
    if (this.mounted && this.props.currLecture) {
      if (this.props.currLecture.cue !== prevProps.currLecture.cue) {
        this.setState({
          cue: this.props.currLecture.cue
        });
      }
    }
  }

  componentWillUnmount() {
    this.mounted = false;
    try {
      this.player.stop();
      this.player.unload();
      this.player.shutdown();
      this.player.off('ready', this.onReady)
      this.player.off("progress", this.onTimeUpdate);
      this.player.off("finish", this.onEnded);
      this.player.off("pause", this.onPaused);
      this.player.off("error", this.onError);
      this.player.off("shutdown", this.onShutdown);
      this.player = null;
    } catch(e) {
      console.log(e);
    }
  }

  onPercentageUpdate = (data) => {
    let percentage = Math.ceil(data.percent * 100);
    let prevTimer = this.state.timer;
    if (data.seconds % 5 !== 0 && this.mounted) {
      this.setState({
        timer: Math.ceil(data.seconds),
        cue: data.seconds,
        percentage: percentage
      }, () => {
        if ((prevTimer !== this.state.timer) || (percentage === 100)) {
          this.props.onPercentageUpdate({
            percentage: percentage,
            cue: Math.ceil(data.seconds),
            lectureId: this.props.currLecture.id,
            completed: data.completed
          });
        }
      });
    }

    if (data.completed && this.mounted) {
      this.props.onComplete();
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.mounted && this.props.swipeEvent !== nextProps.swipeEvent) {
      this.setState({
        swipeEvent: nextProps.swipeEvent
      });
    }
  }

  mouseEnterHandler = () => {
    if (this.timer) {
      clearTimeout(this.timer);
    }

    if (this.mounted) {
      this.setState({
        opacity: 1
      });
    }
  }

  mouseLeaveHandler = () => {
    if (this.mounted && !this.props.currOpen && !this.props.isPreview) {
      this.timer = setTimeout(() => {
        if (this.mounted) {
          this.setState({
            opacity: 0
          });
        }
      }, 4000);
    }
  }

  render() {
    const videoHeight = window.innerHeight - 60 * 2;
    if (this.state.errored) {
      return (
        <div style={{ backgroundColor: '#000' }} className="video-lecture">
          <div className="active-topbar" onMouseEnter={this.mouseEnterHandler} onMouseLeave={this.mouseLeaveHandler}></div>
          <VideoHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent} />
          <div className="lecture-holder">
            <div className="context-holder locked-content">
              <h5 className="locked-title">The video is being processed.</h5>
              <h5 className="locked-title">Please try again later.</h5>
            </div>
          </div>
          <VideoFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent} />
        </div>
      )
    }

    return (
      <div style={{ backgroundColor: '#000' }} className="video-lecture">
        <div className="active-topbar" onMouseEnter={this.mouseEnterHandler} onMouseLeave={this.mouseLeaveHandler}></div>
        <VideoHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent} />
        <div className="video-container"><div id="player" style={{ height: ((this.state.opacity === 1) ? videoHeight : '100%') }} className="flplayer"></div></div>
        <VideoFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent} />
      </div>
    );
  }

}

const mapStateToProps = state => {
  return {
    currLecture: state.currLecture,
    currOpen: state.currOpen,
    dissOpen: state.dissOpen,
    isPreview: state.isPreview,
		courseSlug: state.course.cb_slug
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onPercentageUpdate: (lectureData) => dispatch({ type: actionTypes.UPDATE_PERCENTAGE, payload: lectureData })
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Flow);