import React from 'react';

class Multiple extends React.Component {
    mounted = false;

    state = {
        data : null,
        values : []
    }

    componentDidMount(){
        this.mounted = true;

        if(this.mounted){
            this.setState({
                data : this.props.data
            });
        }
    }

    toggleCheckBox = (value) => {
        let values = [...this.state.values];
        if(values.includes(value)){
            var index = values.indexOf(value);
            if (index > -1) {
                values.splice(index, 1);
            }
        }else{
            values.push(value);
        }
        this.setState({
            values : [...values]
        },() => {
            let options = JSON.parse(this.state.data.sq_options);
            let answers = this.state.values.map((val,v_key) => {
                return options[val];
            });
            let response = {
                id : this.state.data.id,
                question : this.state.data.sq_question,
                type : +this.state.data.sq_type,
                answer : answers
            };
            this.props.recieveResponse(response);
        });
    }

    render(){
        let options = this.state.data?JSON.parse(this.state.data.sq_options):[];
        options = options.map((option,o_key) => {
            return (
                <div key={o_key} className="survey-option-row">
                    <div className="form-group clearfix">
                        <input type="checkbox" name={option} onChange={() => this.toggleCheckBox(o_key)} checked={this.state.values.includes(o_key)} className="multiple-choice-option"/>
                        <label onClick={() => this.toggleCheckBox(o_key)} className="multiple-choice-label">{option}</label>	
                    </div>
                </div>
            );
        });

        let qClasses = ['survey-ques'];
        if(this.state.data && +this.state.data.sq_required === 1){
            qClasses.push('mandatory');
        }

        return(
            <div className="survey-row">
                <div className="survey-form-flex">
                    <div className="form-group ques-no text-right clearfix">Q{this.props.no} :</div>
                    <div className="form-group clearfix">
                        <label className={qClasses.join(' ')}>{this.state.data?this.state.data.sq_question:null}<span className="red-text"></span></label>
                    </div>
                </div>
                <div className="survey-option-wrapper">
                    <div id="option-holder">
                        {options}
                    </div>
                </div>
            </div>
        );
    }
}

export default Multiple;