import React, { Component } from 'react';
import { connect } from 'react-redux';
// import Checkmobile from '../../../utils/services/checkmobile/Checkmobile';
import * as actionTypes from '../../../../store/actions';
import Checkmobile from '../../../utils/services/checkmobile/Checkmobile';
import { redirectToDashboard } from '../../../utils/services/utils';


class VideoHeader extends Component {
    mounted = false;

    state = {
        currLecture: null,
        currLectureId: 0,
    }

    componentDidMount() {
        this.mounted = true;
        if (this.mounted && this.props.currLecture && +this.props.currLecture.id !== +this.state.currLectureId) {
            this.setState({
                currLecture: { ...this.props.currLecture },
                currLectureId: this.props.currLecture.id
            });
        }
        this.props.mouseLeaveHandler();
    }

    componentDidUpdate() {
        this.mounted = true;
        if (this.mounted && typeof this.props.currLecture.id !== 'undefined' && +this.props.currLecture.id !== +this.state.currLectureId) {
            this.setState({
                currLecture: { ...this.props.currLecture },
                currLectureId: this.props.currLecture.id
            });
        }
    }

    componentWillUnmount() {
        this.mounted = false;
    }

    componentWillReceiveProps(nextProps){
        if(this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.props.mouseEnterHandler();
            }else{
                this.props.mouseLeaveHandler();
            }
        }
    }

    curriculumToggleHandler = () => {
        this.props.onCurriculumToggle();
    }

    discussionToggleHandler = () => {
        this.props.onDiscussionToggle();
    }

    backToDashboard = () => {
        if (Checkmobile()) {
            this.props.menuOpenFn();
        } else {
            return redirectToDashboard(this.props.course.id, this.props.isPreview, this.props.courseSlug);
        }
    }

    render() {

        let lectureHeader = <div className="nav-container" onMouseEnter={this.props.mouseEnterHandler} onMouseLeave={this.props.mouseLeaveHandler}>
            <div className="nav-header dark-header" style={{ opacity: this.props.opacity, visibility: this.props.opacity ? 'visible' : 'hidden' }}>
                <div className="menu-icon">
                    <span className="menu" onClick={this.curriculumToggleHandler}></span>
                </div>
                <div className="text-left contenttitle">
                    <span className="lecture-title">{this.state.currLecture ? this.state.currLecture.cl_lecture_name : null}</span>
                    <div className="curriculam-index">
                        <span>Section : {this.state.currLecture ? this.state.currLecture.sNumber : null} &nbsp;</span>
                        <span>Lecture : {this.state.currLecture ? this.state.currLecture.lNumber : null}</span>
                    </div>
                </div>
                <div className="text-right ask-wrapper">
                    {/* <span className="ask-holder" onClick={this.discussionToggleHandler}><span className="ask">Ask</span> <span className="ask-icon">?</span></span> */}
                    <span title="Back to course dashboard." className="close-icon" onClick={this.backToDashboard}></span>
                </div>
            </div>
        </div>;

        return lectureHeader;
    }
}

const mapStateToProps = state => {
    return {
        currLecture: state.currLecture,
        currOpen: state.currOpen,
        course: state.course,
        isPreview: state.isPreview,
        courseSlug: state.course.cb_slug
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onCurriculumToggle: () => dispatch({ type: actionTypes.TOGGLE_CURRICULUM }),
        onDiscussionToggle: () => dispatch({ type: actionTypes.TOGGLE_DISCUSSION }),
        menuOpenFn: () => dispatch({type : actionTypes.SET_MENU_OPEN, payload: true}),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(VideoHeader);