import React,{Component} from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import YoutubePlayer from '@u-wave/react-youtube';
import Axios from '../../../axios/php';
import DocumentHeader from '../document/documentHeader/DocumentHeader';
import DocumentFooter from '../document/documentFooter/DocumentFooter';
import '../document/Document.css';
import { redirectToDashboard } from '../../utils/services/utils';

import VideoHeader from '../video/videoHeader/VideoHeader';
import VideoFooter from '../video/videoFooter/VideoFooter';

const JOIN = "Join Live";
const NOTSTARTED = "Session not yet started";
const EXPIRED = "Expired";

class Live extends Component {
    mounted = false;
    timerObj = null;

    state = {
        opacity : 1,
        timer : null,
        lectureId : false,
        liveData : null,
        loaded : false,
        player : false,
        buttonText: JOIN
    }

    componentDidMount(){
        this.mounted = true;
        this.mouseLeaveHandler();

        if(this.mounted && +this.state.lectureId !== +this.props.lectureId){
            this.setState({
                lectureId : this.props.lectureId
            },() => {
                this.setLiveData().catch(e => console.log(e));
            });
        }
    }

    mouseEnterHandler = () => {
        if (this.state.timer) {
            clearTimeout(this.state.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        if (this.mounted && !this.props.currOpen && !this.props.isPreview) {
            const timer = setTimeout(() => {
                if (this.mounted) {
                    this.setState({
                        opacity: 0
                    });
                }
            }, 4000);

            this.setState({
                timer: timer
            });
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.mounted && nextProps.lectureId && +this.props.lectureId !== +nextProps.lectureId){
            this.setState({
                lectureId : nextProps.lectureId,
                loaded : false,
                liveData : null
            },() => {
                this.setLiveData().catch(e => console.log(e));
            });
        }

        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.mouseEnterHandler();
            }else{
                this.mouseLeaveHandler();
            }
        }
    }

    checkStatus() {
        let { liveData } = this.state;
        if(liveData && this.mounted) {
            const lectureStartDate = new Date(`${liveData.ll_date}T${liveData.ll_time}`).getTime();
            const now = Date.now();
            const diff = (now - lectureStartDate)/60000;
            const llDuration = parseFloat(liveData.ll_duration);
            // console.log(now, lectureStartDate, diff);
            if(diff > llDuration){
                liveData.attentable = false;
                this.setState({ liveData, buttonText: EXPIRED })
                return;
            }
            if(diff < 0){
                liveData.attentable = false;
                this.setState({ liveData, buttonText: NOTSTARTED })
                return;
            }
            liveData.attentable = true;
            this.setState({ liveData, buttonText: JOIN });
        }
    }

    setLiveData = () => {
        return new Promise((resolve, reject) => {
            let request = {
                header: {
                    method: "lecture_detail"
                },
                body: {
                    course_id : this.props.courseId,
                    lecture_id : this.state.lectureId,
                    lecture_type : 7
                }
            };
            if(this.mounted){
                this.setState({
                    assessLoading : true
                });
            }
            Axios.post(`/content`,JSON.stringify(request))
            .then(response => {
                //console.log(response.data.body.lecture);
                if(!response.data.header.error){
                    if(this.mounted){
                        this.setState({
                            liveData : {...response.data.body.lecture},
                            loaded : true,
                            player : false
                        },() => {
                            resolve(this.state.liveData);
                            this.checkStatus();
                            this.timerObj = setInterval(() => { this.checkStatus(); }, 60000);
                        });
                    }
                }else{
                    if(response.data.header.code === 604){
                        return redirectToDashboard(this.props.courseId, this.props.isPreview, this.props.courseSlug);
                    }else{
                        reject(response.data.header.message);
                    }
                }
            })
            .catch(err => {
                reject(err);
            });
        });
    }

    showLive = () => {
        if(this.mounted && this.state.liveData && this.state.liveData.attentable){
            this.setState({
                player : true
            });
        }
    }

    youtube_parser(url){
        let video_id = url.split('v=')[1];
        const ampersandPosition = video_id.indexOf('&');
        if(ampersandPosition !== -1) {
            video_id = video_id.substring(0, ampersandPosition);
        }
        return video_id;
    }
    
    componentWillUnmount(){
        this.mounted = false;
        clearInterval(this.timerObj);
    }

    render(){
        let content = (
            <div className="preview-area custom-scroller">
                <div className="active-topbar" onMouseEnter={this.mouseEnterHandler} onMouseLeave={this.mouseLeaveHandler}></div>
                <DocumentHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
                <div className="context-holder" style={{marginTop : '180px'}}>
                    <div id="result" className="tabcontent context custom-scroller">
                        <span className="instruction-info date-info-wrapper">
                            <span className="submision-date"><span className="live-date-title">Date :</span> {this.state.loaded?this.state.liveData.date_formatted:null}</span>
                        </span>
                        <div className="result-wrapper duration-info-col">
                            <div className="result-info duration-wrapper">
                                <span className="marks live-duration" style={{fontSize:'16px'}}><span className="live-duration-title">Duration : </span>{this.state.loaded?this.state.liveData.ll_duration+'-Min':null}</span>
                            </div>
                        </div>
                        <div className="text-center btn-container">
                            <button className="btn-violet" onClick={(this.state.liveData && this.state.liveData.attentable)?this.showLive:null} style={{cursor : (this.state.liveData && this.state.liveData.attentable)?'pointer':'not-allowed'}} type="">{this.state.buttonText}</button>
                        </div>
                    </div>
                </div>
                <DocumentFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
            </div>
        );

        if(this.state.player && this.state.liveData){
            const videoHeight = window.innerHeight - 60 * 2;
            let videoId = this.youtube_parser(this.state.liveData.stream);
            content = (
                <div style={{backgroundColor: '#444'}} className="video-lecture">
                    <div className="active-topbar" onMouseEnter={this.mouseEnterHandler} onMouseLeave={this.mouseLeaveHandler}></div>
                    <VideoHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity}/>
                    <div className="video-container">
                        <YoutubePlayer
                            id={'youtube'}
                            width={'100%'}
                            height={(this.state.opacity === 1) ? videoHeight : '100%'}
                            video={videoId}
                            controls={true}
                            autoplay={true}
                            modestBranding={true}
                            showRelatedVideos={false}
                        />
                    </div>
                    <VideoFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
                </div>
            );
        }

        return content;
    }
}

const mapStateToProps = state => {
    return {
        courseId : state.subscription.id?state.subscription.cs_course_id:0,
        currLecture : state.currLecture,
        currOpen : state.currOpen,
        isPreview : state.isPreview,
        courseSlug: state.course.cb_slug
    }
}

export default connect(mapStateToProps,null)(withRouter(Live));