import React from 'react';

class Range extends React.Component {
    mounted = false;

    state = {
        data : null,
        selected : null
    }

    componentDidMount(){
        this.mounted = true;

        if(this.mounted){
            this.setState({
                data : this.props.data
            },() => {
                // console.log(this.state.data);
            });
        }
    }

    changeOption = (selectedOption) => {
        this.setState({
            selected : selectedOption
        },() => {
            let response = {
                id : this.state.data.id,
                question : this.state.data.sq_question,
                type : +this.state.data.sq_type,
                answer : selectedOption
            };
            this.props.recieveResponse(response);
        });
    }

    render(){
        let qClasses = ['survey-ques'];
        if(this.state.data && +this.state.data.sq_required === 1){
            qClasses.push('mandatory');
        }

        let i = this.state.data?+this.state.data.sq_low_limit:0;
        let limit = this.state.data?+this.state.data.sq_high_limit:0;
        if(i === 0){
            limit++;
        }
        let td = new Array(limit).fill(null);
        let th = new Array(limit).fill(null);

        th = th.map((data,key) => {
            return <th key={key}>{i===1?key+1:key}</th>;
        });

        td = td.map((data,key) => {
            return  <td key={key}>
                        <input type="radio" name={`${this.props.no}_${(i===1?key+1:key)}`} onChange={() => this.changeOption(i===1?key+1:key)} checked={this.state.selected === (i===1?key+1:key)} className="linear-choice-option"/>
                        <label onClick={() => this.changeOption(i===1?key+1:key)} className="linear-choice-label"></label>
                    </td>;
        });

        return(
            <div className="survey-row">
                <div className="survey-form-flex">
                    <div className="form-group ques-no text-right clearfix">Q{this.props.no} :</div>
                    <div className="form-group clearfix">
                    <label className={qClasses.join(' ')}>{this.state.data?this.state.data.sq_question:null}</label>
                    </div>
                </div>
                <div className="survey-option-wrapper">
                    <div id="option-holder">
                        <div className="survey-option-row">
                            <table className="linear-choice-table">
                                <thead>
                                    <tr>
                                        {th}
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        {td}
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Range;