import phpAxios from '../../../axios/php';

const LOCAL_URL = process.env.REACT_APP_LOCAL_URL;
export const BASEURL = LOCAL_URL ? LOCAL_URL : (window.location.protocol + '//' + window.location.host);
export const HOST = window.location.host ? window.location.host : (new URL(BASEURL)).host;

export const toHHMMSS = function (num, hflag) {
  var sec_num = parseInt(num, 10); // don't forget the second param
  var hours   = Math.floor(sec_num / 3600);
  var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
  var seconds = sec_num - (hours * 3600) - (minutes * 60);

  if (hours   < 10) {hours   = "0"+hours;}
  if (minutes < 10) {minutes = "0"+minutes;}
  if (seconds < 10) {seconds = "0"+seconds;}
  return hflag ? (hours+':'+minutes+':'+seconds) : (minutes+':'+seconds);
}

export function fullScreen() {
  let el = document.documentElement , rfs = el.requestFullScreen || el.webkitRequestFullScreen || el.mozRequestFullScreen || el.msRequestFullscreen; rfs.call(el); 
}

export function exitFullScreen() {
  let el = document , rfs = el.exitFullscreen || el.webkitExitFullscreen || el.mozCancelFullScreen || el.msExitFullscreen; rfs.call(el); 
}

export function f_open_window_max(aURL, aWinName) {
  var wOpen;
  var sOptions;

  sOptions = 'status=0,toolbar=0';
  sOptions = sOptions + ',width=' + (window.screen.availWidth - 10).toString();
  sOptions = sOptions + ',height=' + (window.screen.availHeight - 122).toString();
  sOptions = sOptions + ',screenX=0,screenY=0,left=0,top=0';

  wOpen = window.open('', aWinName, sOptions);
  wOpen.location = aURL;
  wOpen.focus();
  wOpen.moveTo(0, 0);
  wOpen.resizeTo(window.screen.availWidth, window.screen.availHeight);
  return wOpen;
}

