export function getPreviewDetails(courseId, isPreview, slug) {
  const request = {
    header : {
      service: "",
      method: "get_course_preview_time"
    },
    body : {
      course_id : courseId
    }
  };
  return phpAxios.post('/content',JSON.stringify(request))
  .then(response => {
    if(!response.data) {
      return redirectToDashboard(courseId, isPreview, slug)
    }
    if(!response.data.header.error){
    } else if(response.data.header.code === 604){
      return redirectToDashboard(courseId, isPreview, slug)
    }
    return response;
  });
}

export function setCookie(cname, cvalue, exdays) {
  var d = new Date();
  d.setTime(d.getTime() + (exdays*24*60*60*1000));
  var expires = "expires=" + d.toGMTString();
  document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
}

export function getCookie(cname) {
  var name = cname + "=";
  var ca = document.cookie.split(';');
  for(var i = 0; i < ca.length; i++) {
    var c = ca[i];
    while (c.charAt(0) === ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}