import React,{Component} from 'react';
import {connect} from 'react-redux';
import {withRouter,Link} from 'react-router-dom';
import Aux from '../../../../hoc/Aux/Aux';
import lock from '../../../../assets/images/baseline-lock-24px.svg';
import {toHHMMSS} from '../../../utils/services/utils';

class Lecture extends Component {

    componentDidUpdate(prevProps) {
        if(prevProps.currLecId !== this.props.currLecId) {
            const activeTimeline = document.querySelector('.active-timeline');
            if(activeTimeline) { activeTimeline.scrollIntoView(); }
        }
    }

    render() {

        let tailHtml = null;
        let statusIcon = null;

        if(this.props.locked) {
            statusIcon = <p className="curriculam-info-duration text-right">
                <img src={lock} alt="Locked" width="20" title="Locked" />
            </p>;
        } else if(parseInt(this.props.duration)) {
            statusIcon = <p className="curriculam-info-duration text-right" title="Duration">{toHHMMSS(this.props.duration, true)}</p>
        }

        if(!this.props.first && !this.props.last){
            tailHtml = <Aux><div className="tail-up"></div><div className="tail-down"></div></Aux>;
        }else{
            if(this.props.last && !this.props.first){
                tailHtml = <div className="tail-up"></div>;
            }

            if(this.props.first && !this.props.last){
                tailHtml = <div className="tail-down"></div>;
            }
        }

        let mainClass = ['timeline-row'];
        const iconClasses = {
            1 : 'video',
            2 : 'document',
            3 : 'quiz',
            4 : 'video',
            5 : 'code',
            7 : 'video',
            8 : 'assignment',
            9 : 'video',
            10 : 'drop',
            11 : 'video',
            12 : 'audio',
            14 : 'certificate'
        };
        let icon = iconClasses[this.props.type];

        if(this.props.attempts > 0 || this.props.percentage > 99){
            mainClass[mainClass.length] = 'completed-curriculam';
            icon = icon+'-green';
        }else{
            if(this.props.percentage === 0 && this.props.percentage === 0){
                icon = icon+'-grey';
            }else{
                icon = icon+'-blue';
            }
        }
        const iconClass = ['milestone-icon',icon];
        if(+this.props.currLecId === +this.props.lectureId){
            mainClass[mainClass.length] = 'active-timeline';
        }

        return(
            <div className={mainClass.join(' ')}>
                <Link to={`/${this.props.courseId}/${this.props.lectureId}`}>
                    <span className="curriculam-progress" style={{width : this.props.percentage+'%'}}></span>
                    <div className="milestone-holder">
                        <span className="milestone">
                            {(this.props.attempts > 0 || this.props.percentage > 99)?<span className="ticked"></span>:null}
                            <span className={iconClass.join(' ')}></span>
                        </span>
                        {tailHtml}
                    </div>
                    <div className="timeline-text text-justify">
                        <p className="curriculam-info-text">{this.props.lectureName}</p>
                        {statusIcon}
                        {this.props.totalAttempts === 0?null:
                            <p className="curriculam-info-view text-right orange-text">
                                <span className="orange-eye"></span> {this.props.attempts} / {this.props.totalAttempts}
                            </p>}
                    </div>
                </Link>
            </div>
        );
    }
};

const mapStateToProps = state => {
    return {
        currLecId : state.currLecId,
        isPreview: state.isPreview
    };
};

export default connect(mapStateToProps,null)(withRouter(Lecture));