import React,{Component} from 'react';
import DocumentHeader from '../document/documentHeader/DocumentHeader';
import DocumentFooter from '../document/documentFooter/DocumentFooter';
import LockImg from '../../../assets/images/lock.png';
import '../document/Document.css';

class Locked extends Component {
    mounted = false;

    state = {
        opacity : 1,
        timer : null,
        reasons : null
    }

    componentDidMount(){
        this.mounted = true;
        this.mouseLeaveHandler();

        if(this.mounted){
            this.setState({
                reasons : [...this.props.reasons]
            });
        }
    }

    componentDidUpdate(prevProps,prevState){
        if(this.mounted && JSON.stringify(prevProps.reasons) !== JSON.stringify(this.props.reasons)){
            this.setState({
                reasons : [...this.props.reasons]
            });
        }
    }

    mouseEnterHandler = () => {
        if (this.state.timer) {
            clearTimeout(this.state.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        // if (this.mounted && !this.props.currOpen) {
        //     const timer = setTimeout(() => {
        //         if (this.mounted) {
        //             this.setState({
        //                 opacity: 0
        //             });
        //         }
        //     }, 4000);

        //     this.setState({
        //         timer: timer
        //     });
        // }
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    render(){
        let reasons = this.state.reasons?this.state.reasons:[];

        reasons = reasons.map((reason,r_key) => {
            return <li key={r_key} dangerouslySetInnerHTML={{__html: reason}}></li>
        });

        return(
            <div className="preview-area custom-scroller">
                <DocumentHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} locked={true} />
                <div className="lecture-holder">
                    <div className="context-holder locked-content">
                        <div className="text-center"><img className="lock-img" src={LockImg} alt=""/></div>
                        <h5 className="locked-title">Locked Content</h5>
                            <ol className="locked-content-info text-center">
                                {reasons}
                            </ol>
                    </div>
                </div>
                <DocumentFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
            </div>
        );
    }
}

export default Locked;