import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import * as actionTypes from '../../store/actions';
import { redirectToDashboard } from '../../components/utils/services/utils';

class Menu extends Component {
    mounted = false;

    componentDidMount(){
        this.mounted = true;
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    handleFloatClick = () => {
        this.props.menuClose();
    }

    _backToDashboard = () => {
        return redirectToDashboard(this.props.course.id, this.props.isPreview, this.props.courseSlug);
    }

    render() {
        const menuClass = this.props.menuOpen ? 'float-ui active' : 'float-ui';
        return (
            <div className={menuClass} onClick={this.handleFloatClick}>
                <ul>
                    <li onClick={this.props.onfullScreenClick}>Fullscreen</li>
                    <li onClick={this._backToDashboard}>Exit to Dashboard</li>
                    <li>Cancel</li>
                </ul>
            </div>
        );
    }

}

function mapStateToProps(state) {
    return {
        menuOpen : state.menuOpen,
        course : state.course,
        isPreview : state.isPreview,
		courseSlug: state.course.cb_slug
    }
}

function mapDispatchToProps(dispatch) {
    return {
        menuClose: () => dispatch({type : actionTypes.SET_MENU_OPEN, payload: false}),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Menu));