import React from 'react';

class Dropdown extends React.Component {
    mounted = false;

    state = {
        data : null,
        value : null
    }

    componentDidMount(){
        this.mounted = true;

        if(this.mounted){
            this.setState({
                data : this.props.data
            });
        }
    }

    changeDropdown = (eve) => {
        this.setState({
            value : eve.target.value
        },() => {
            let options = JSON.parse(this.state.data.sq_options);
            let response = {
                id : this.state.data.id,
                question : this.state.data.sq_question,
                type : +this.state.data.sq_type,
                answer : options[this.state.value]
            };
            this.props.recieveResponse(response);
        })
    }

    render(){
        let options = this.state.data?JSON.parse(this.state.data.sq_options):[];
        options = options.map((option,o_key) => {
            return (
                <option key={o_key} defaultValue={this.state.value === o_key} value={o_key}>{option}</option>
            );
        });

        let qClasses = ['survey-ques'];
        if(this.state.data && +this.state.data.sq_required === 1){
            qClasses.push('mandatory');
        }

        return(
            <div className="survey-row">
                <div className="survey-form-flex">
                    <div className="form-group ques-no text-right clearfix">Q{this.props.no} :</div>
                    <div className="form-group clearfix">
                        <label className={qClasses.join(' ')}>{this.state.data?this.state.data.sq_question:null}<span className="red-text"></span></label>
                    </div>
                </div>
                <div className="survey-option-wrapper">
                    <div id="option-holder">
                        <div className="survey-option-row">
                            <select onChange={this.changeDropdown} className="option-selectbox">
                                <option value="select">Select</option>
                                {options}
                            </select>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Dropdown;