import React,{Component} from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';
import * as actionTypes from '../../../store/actions';
import Aux from '../../../hoc/Aux/Aux';
import Axios from '../../../axios/php';
import DocumentHeader from '../document/documentHeader/DocumentHeader';
import DocumentFooter from '../document/documentFooter/DocumentFooter';
import StickyHeader from '../assignment/stickyHeader/StickyHeader';
import { f_open_window_max, redirectToDashboard } from '../../utils/services/utils';

class Assessment extends Component
{
    mounted = false;

    state = {
        activeTab : 0,
        opacity : 1,
        timer : null,
        courseId : 0,
        lectureId : 0,
        assessmentData : null,
        childWindow : null,
        windowTimer : null,
        testProgress : false,
        assessLoading : true,
        readMore : true
    }

    tabSwitchHandler = (tab) => {
        if(this.mounted){
            this.setState({
                activeTab : tab
            });
        }
    }

    componentDidMount(){
        this.mouseLeaveHandler();
        this.mounted = true;
        let tab = this.props.match.params.tab ? parseInt(this.props.match.params.tab) : 0;
        tab = isNaN(tab) ? 0 : tab;
        tab = ([0, 1].indexOf(tab) > -1) ? tab : 0;

        if(this.mounted && +this.state.lectureId !== +this.props.lectureId){
            this.setState({
                courseId : this.props.courseId,
                lectureId : this.props.lectureId,
                activeTab: tab
            },() => {
                this.setAssessmentData().catch(e => console.log(e));
            });
            this.mouseLeaveHandler();
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.mounted && this.props.lectureId && +this.state.lectureId !== +this.props.lectureId){
            this.setState({
                lectureId : this.props.lectureId,
                readMore : true
            },() => {
                this.setAssessmentData().catch(e => console.log(e));
            });
        }

        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.mouseEnterHandler();
            }else{
                this.mouseLeaveHandler();
            }
        }
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    setAssessmentData = () => {
        return new Promise((resolve, reject) => {
            const request = {
                header : {
                    service: "",
                    method: "assessment"
                },
                body : {
                    course_id : this.props.match.params.courseId,
                    lecture_id : this.state.lectureId
                }
            };
            if(this.mounted){
                this.setState({
                    assessLoading : true
                });
            }
            Axios.post(`/content`,JSON.stringify(request))
            .then(response => {
                // console.log(response);
                if(!response.data) { return reject(); }
                if(!response.data.header.error){
                    if(this.mounted){
                        this.setState({
                            assessmentData : {...response.data.body},
                            assessLoading : false
                        },() => {
                            resolve(this.state.assessmentData);
                        });
                    }
                }else{
                    if(response.data.header.code === 604){
                        return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
                    }else{
                        reject(response.data.header.message);
                    }
                }
            })
            .catch(err => {
                reject(err);
            });
        });
    }

    mouseEnterHandler = () => {
        if(this.state.timer){
            clearTimeout(this.state.timer);
        }
        
        if(this.mounted){
            this.setState({
                opacity : 1
            });
        }
    }

    mouseLeaveHandler = () =>{
        // if(this.mounted && !this.props.currOpen){
        //     const timer = setTimeout(() => {
        //         if(this.mounted){
        //             this.setState({
        //                 opacity : 0
        //             });
        //         }
        //     },4000);
            
        //     this.setState({
        //         timer : timer
        //     });
        // }
    }

    

    checkChildWindow = () => {
        if (this.mounted && this.state.childWindow.closed) {
            this.setState({
                testProgress : false
            },() => {
                this.setAssessmentData().then(resp => {
                    if(+resp.assessment.attempt_data.aa_completed){
                        this.props.onPercentageUpdate({
                            percentage : 100,
                            cue : 0,
                            lectureId : this.props.lectureId,
                            completed : true
                        });
                    }
                }).catch(e => console.log(e));
            });
            clearInterval(this.state.windowTimer);
        }
    }

    startAssessmentHandler = (assessmentLink) => {
        // let childWindow = window.open(assessmentLink, 'Assesment - SDPK', 'toolbar=0,status=0,width='+window.screen.availWidth+',height='+window.screen.availWidth+'');
        const assessParts = assessmentLink.split("?");
        const op = assessParts[1] ? '&' : '?';
        const assessLink = this.props.token ? `${assessmentLink + op}token=${this.props.token}` : assessmentLink;
        let childWindow = f_open_window_max(assessLink, 'Assessment - SDPK')
        var timer = setInterval(this.checkChildWindow, 500);
        this.setState({
            testProgress : true,
            childWindow : childWindow,
            windowTimer : timer
        });
    }

    readMore = () => {
        if(this.mounted){
            this.setState({
                readMore : false
            });
        }
    }

    render(){
        const config = this.props.config;
        let Instructions = null;
        
        let Result = null;
        let attemptData = {};

        if(this.state.assessmentData){
            const assessData = {...this.state.assessmentData.assessment.a_data};
            let assessmentLink = config.assessmentUrl.replace("{testId}",assessData.id);
            attemptData = {...this.state.assessmentData.assessment.attempt_data};
            let reportLink = null;
            let valuated = true;
            if(typeof attemptData.id !== 'undefined'){
                reportLink = config.apiUrl+`/material/assesment_report_item/${attemptData.id}`;
                if(+attemptData.aa_valuated){
                    valuated = true;
                }else{
                    valuated = false;
                }
            }
            let reportLinkContent = null;
            
            if(valuated && +attemptData.aa_completed){
                if(reportLink){
                    reportLinkContent = <a target="_blank" rel="noopener noreferrer" className="view-report-btn" href={reportLink}>View Report</a>;
                }
            }else{
                if(+attemptData.aa_completed){
                    reportLinkContent = <span className="view-report-btn">Waiting Result</span>
                }else{
                    reportLinkContent = <span className="view-report-btn">Test on Progress</span>
                }
            }
            let classes = ['tabcontent','context','custom-scroller'];
            if(this.state.readMore){
                classes.push('content-read-toggle');
            }
            const assessInstr = JSON.parse(assessData.a_instructions);
            Instructions = <Aux>
                                <div id="instruction" className={classes.join(' ')}>
                                    <div className="quiz-instruction-tags">
                                        <div><span>{assessData.a_questions}</span><span>Questions</span></div>
                                        <div><span>{assessData.a_duration}</span><span>Min</span></div>
                                        <div><span>{assessData.a_mark}</span><span>Marks</span></div>
                                    </div>
                                    <div className="locked-content-info text-center" dangerouslySetInnerHTML={{__html: assessInstr["1"]}}>
                                    </div>
                                    {this.state.readMore?<div onClick={this.readMore} className="read-more">Read More</div>:null}
                                </div>
                                <div className="text-center btn-container">
                                    <button style={{cursor : !this.state.testProgress&&this.state.assessmentData.assessment.attemptable?'pointer':'not-allowed'}} onClick={() => !this.state.testProgress&&this.state.assessmentData.assessment.attemptable?this.startAssessmentHandler(assessmentLink):null} className="btn-violet">{this.state.testProgress?'On Progress':(+attemptData.aa_completed === 0?'Resume Quiz':'Start Quiz')}</button>
                                </div>
                            </Aux>;

            Result = <div id="result" className="tabcontent context custom-scroller">
                        <span className="instruction-info"> 
                            <span className="submision-date">{typeof attemptData.id !== 'undefined'?attemptData.aa_attempted_date:' - '}</span>
                        </span>
                        <div className="result-wrapper">
                            <div className="result-info">
                                <span className="marks">{typeof attemptData.id !== 'undefined' && attemptData.aa_mark_scored && valuated?attemptData.aa_mark_scored:' - '}</span>
                                <span className="result-info-title">Marks Scored</span>
                            </div>
                            <div className="result-info">
                                <span className="quiz-timetaken">{typeof attemptData.id !== 'undefined'&&attemptData.aa_duration !== '00:00' && valuated?attemptData.aa_duration:' - '}</span>
                                <span className="result-info-title">Time Taken</span>
                            </div>
                            <div className="result-info">
                                <span className="grade">{typeof attemptData.id !== 'undefined' && valuated?attemptData.aa_grade:' - '}</span>
                                <span className="result-info-title">Grade</span>
                            </div>		
                        </div>
                        <div className="text-center btn-container">
                            {reportLinkContent}
                            <button className="btn-violet" style={{cursor : !this.state.testProgress&&this.state.assessmentData.assessment.attemptable?'pointer':'not-allowed'}} onClick={() => !this.state.testProgress&&this.state.assessmentData.assessment.attemptable?this.startAssessmentHandler(assessmentLink):null} type="">{+attemptData.aa_completed?'Retry Quiz':'Resume Quiz'}</button>
                        </div>
                    </div>;
        }
        
        const Content = this.state.activeTab === 0?Instructions:Result;

        let classes = [];
        classes[0] = ['tab','tablinks'];
        classes[1] = ['tab','tablinks'];
        classes[this.state.activeTab].push('active');

        return(
            <div className="preview-area custom-scroller">
                <StickyHeader/>
                <DocumentHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity}/>
                <div className="lecture-holder quiz-content text-center">
                    <div><h2>Quiz</h2></div>
                    <div className="content-toggle-tab" style={{display:'inline-block'}}>
                        <div className={classes[0].join(' ')} onClick={() => this.tabSwitchHandler(0)}>Instructions</div>
                        <div style={{cursor: typeof attemptData.id !== 'undefined'?'pointer':'not-allowed'}} disabled={typeof attemptData.id !== 'undefined'} className={classes[1].join(' ')} onClick={() => typeof attemptData.id !== 'undefined'?this.tabSwitchHandler(1):null}>Result</div>
                    </div>
                    <div className="context-holder">
                        {this.state.assessLoading?null:Content}
                    </div>
                </div>
                <DocumentFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity}/>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        config: state.config,
        courseId : state.subscription.id?state.subscription.cs_course_id:0,
        lectureId : state.currLecId,
        currOpen : state.currOpen,
        isPreview: state.isPreview,
        courseSlug: state.course.cb_slug,
        token: state.token
    };
}

const mapDispatchToProps = dispatch => {
    return {
		onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData})
	};
};

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Assessment));