import React from 'react';
import { connect } from 'react-redux';
import VideoHeader from '../video/videoHeader/VideoHeader';
import VideoFooter from '../video/videoFooter/VideoFooter';

class Recorded extends React.Component {
    mounted = false;
    timer = null;

    state = {
        currLecture : null,
        timer : 0,
        cue : 0,
        percentage : 0,
        swipeEvent : false,
        opacity: 1
    }

    componentDidMount(){
        this.mounted = true;
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    componentWillReceiveProps(nextProps)
    {
        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            this.setState({
                swipeEvent : nextProps.swipeEvent
            });
        }
    }

    mouseEnterHandler = () => {
        if (this.timer) {
            clearTimeout(this.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        // if (this.mounted && !this.props.currOpen) {
        //     this.timer = setTimeout(() => {
        //         if (this.mounted) {
        //             this.setState({
        //                 opacity: 0
        //             });
        //         }
        //     }, 4000);
        // }
    }

    render(){
        return (
            <div style={{backgroundColor: '#444'}} className="video-lecture">
                <VideoHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent}/>
                <h4 className="instruction-info">Available later.</h4>
                <VideoFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} swipeEvent={this.state.swipeEvent} />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        currLecture : state.currLecture,
        currOpen : state.currOpen,
        dissOpen : state.dissOpen
    };
};

export default connect(mapStateToProps,null)(Recorded);