import React,{Component} from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import * as actionTypes from '../../../store/actions';
import DocumentHeader from '../document/documentHeader/DocumentHeader';
import DocumentFooter from '../document/documentFooter/DocumentFooter';
import Generate from './generate/Generate';
import Download from './download/Download';
import '../document/Document.css';
import { BASEURL, redirectToDashboard } from '../../utils/services/utils';

import Axios from '../../../axios/php';

const PROFILEPAGE = `${BASEURL}/dashboard/profile`;

class Certificate extends Component {
    mounted = false;

    state = {
        opacity : 1,
        timer : null,
        certUrl : null,
        generating : false,
        currLecture : null,
        loaded : false,
        modalClasses: 'modal-container'
    }

    componentDidMount(){
        this.mounted = true;
        this.mouseLeaveHandler();

        if(this.mounted){
            this.setState({
                currLecture : this.props.currLecture,
            },() => {
                if(this.props.match.params.lectureId){ this.getCert(); }
            });
        }
    }

    componentDidUpdate(){
        if(this.mounted && JSON.stringify(this.state.currLecture) !== JSON.stringify(this.props.currLecture)){
            this.setState({
                currLecture : this.props.currLecture,
                certUrl : null,
                generating : false,
                loaded : false
            },() => {
                if(this.props.match.params.lectureId){ this.getCert(); }
            });
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.mouseEnterHandler();
            }else{
                this.mouseLeaveHandler();
            }
        }
    }

    getCert = () => {
        const request = {
            header: {
                service: "",
                method: "download_certificate"//"create_document","download_certificate"
            },
            body: {
                course_id: this.props.match.params.courseId,
                lecture_id: this.props.match.params.lectureId
            }
        };

        Axios.post(`/content`, JSON.stringify(request))
            .then(response => {
                if (this.mounted){
                    this.setState({
                        loaded : true
                    });
                }
                if (!response.data.header.error) {
                    if (this.mounted && response.data.body.url !== '') {
                        this.setState({
                            certUrl : response.data.body.url
                        });
                    }
                } else {
                    //console.log(response.data.header.message)
                }
            })
            .catch(err => {
                //console.log(err);
            });
    }

    mouseEnterHandler = () => {
        if (this.state.timer) {
            clearTimeout(this.state.timer);
        }

        if (this.mounted) {
            this.setState({
                opacity: 1
            });
        }
    }

    mouseLeaveHandler = () => {
        // if (this.mounted && !this.props.currOpen) {
        //     const timer = setTimeout(() => {
        //         if (this.mounted) {
        //             this.setState({
        //                 opacity: 0
        //             });
        //         }
        //     }, 4000);

        //     this.setState({
        //         timer: timer
        //     });
        // }
    }
    
    componentWillUnmount(){
        this.mounted = false;
    }

    generateCert = () => {
        const request = {
            header: {
                service: "",
                method: "create_document"//"create_document","download_certificate"
            },
            body: {
                course_id: this.props.match.params.courseId,
                lecture_id: this.props.match.params.lectureId,
                cl_filename : this.state.currLecture.cl_filename
            }
        };

        this.setState({
            generating : true,
            modalClasses: 'modal-container'
        });

        Axios.post(`/content`, JSON.stringify(request))
            .then(response => {
                if (!response.data.header.error) {
                    if (this.mounted) {
                        this.setState({
                            generating : false
                        },() => {
                            this.getCert();
                            this.props.onPercentageUpdate({
                                percentage : 100,
                                cue : 0,
                                lectureId : +this.state.currLecture.id,
                                completed : true
                            });
                        });
                    }
                } else {
                    if(response.data.header.code === 604){
                        return redirectToDashboard(this.props.match.params.courseId, this.props.isPreview, this.props.courseSlug);
                    }
                    //console.log(response.data.header.message);
                    this.setState({
                        generating : false
                    });
                }
            })
            .catch(err => {
                //console.log(err);
            });
    }

    downloadCert = () => {
        if(this.state.certUrl){
            // window.open(this.state.certUrl,'_blank');
            var a = document.createElement('a');
            a.href = this.state.certUrl;
            a.download = 'Certificate';
            a.click();
        }
    }

    cancelHandler = () => {
        this.setState({ modalClasses: 'modal-container' });
    }

    modalOpenHandler = () => {
        this.setState({ modalClasses: 'modal-container visible' })
    }

    render(){
        let modClasses = this.state.modalClasses;
        let userName = '';
        if(this.props.currOpen) { modClasses += ' curropen' }
        if(this.props.dissOpen) { modClasses += ' forumopen' }

        try {
            const userDetails = JSON.parse(this.props.config.user_details);
            userName = userDetails.us_name
        } catch(e) {}

        return(
            <div className="preview-area custom-scroller">
                <DocumentHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
                {
                    this.state.loaded?(this.state.certUrl?
                        <Download
                            certUrl={this.state.certUrl}
                            downloadCert={this.downloadCert}
                        />:
                        <Generate
                            generating={this.state.generating}
                            generate={this.modalOpenHandler}
                        />):null
                }
                <div className={((this.state.modalClasses.indexOf('visible') > -1) ? "lecture-holder certificate-holder" : "")}>
                    <div className="context-holder certificate-context">
                        <div className={modClasses}>
                            <div className="modal-wrapper">
                                <div className="popup-content">
                                    <h3>Confirm Your Name</h3>
                                    <form>
                                        <div className="form-group">
                                            <label htmlFor="user_name">Name</label>
                                            <input type="text" className="form-control" id="user_name" disabled value={userName} placeholder="Name" />
                                            <p>Please <a target="_blank" rel="noopener noreferrer" href={PROFILEPAGE}>follow this link</a> to change your name.</p>
                                        </div>
                                        <div className="confirm-btn-wrapper">
                                            <button className="pop-btn-cancel" onClick={this.cancelHandler} type="button">Cancel</button>
                                            <button className="pop-btn-ok" onClick={this.generateCert} type="button">{this.state.generating?'Generating...':'Generate Now'}</button>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <DocumentFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        currLecture : state.currLecture,
        config: state.config,
        currOpen: state.currOpen,
        dissOpen: state.dissOpen,
        isPreview: state.isPreview,
        courseSlug: state.course.cb_slug
    }
}

const mapDispatchToProps = dispatch => {
    return {
		onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData})
	};
};

export default connect(mapStateToProps,mapDispatchToProps)(withRouter(Certificate));