import React,{Component} from 'react';
import Lecture from './lecture/Lecture';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';

class Lectures extends Component {

    mounted = false;

    state = {
        lectures : []
    }

    componentDidMount (){
        this.mounted = true;
        if(this.mounted){
            this.setState({
                lectures : this.props.lectures
            });
        }
    }

    componentDidUpdate(){
        if(this.mounted && !this.state.lectures.length && this.props.lectures.length){
            this.setState({
                lectures : this.props.lectures
            });
        }
    }

    componentWillReceiveProps(){
        if(this.mounted){
            this.setState({
                lectures : this.props.lectures
            });
        }
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    canPlayLecture = (studentLog, access) => {

        const _permission = { canplay: true, reason: [] };
        let canPlayPermissions = {};
        //checking attempt limit exceeded
        if (typeof studentLog['log'][studentLog['lecture_id']] != "undefined") {
            const lecture = studentLog['log'][studentLog['lecture_id']];
            const courseAccessLimit = +access['access_limit'];
            const views = lecture['nviews'] ? lecture['nviews'] : lecture['views'];
            if (courseAccessLimit !== 0 && (views >= courseAccessLimit)) {
                _permission['canplay'] = false;
                return _permission;
            }
        }
        //End

        //checking course completion 
        const coursePercentageAccess = access['course_percentage'];
        if (typeof coursePercentageAccess !== 'undefined' && +coursePercentageAccess['active'] === 1) {
            canPlayPermissions.coursePC = true;
            if(parseInt(coursePercentageAccess['percentage']) > parseInt(studentLog['overall_percentage'])) {
                _permission['canplay'] = false;
                canPlayPermissions.coursePC = false;
            }
        }
        //End

        //checking start date and time of access 
        const availableFromAccess = access['available_from'];
        // console.log(availableFromAccess);
        if (typeof availableFromAccess !== 'undefined' && +availableFromAccess['active'] === 1) {
            canPlayPermissions.availFrom = true;
            if(new Date(availableFromAccess['timestamp']) > new Date()) {
                _permission['canplay'] = false;
                canPlayPermissions.availFrom = false;
            }
        }
        //End

        //checking end date and time of access 
        const availableTillAccess = access['available_till'];
        if (typeof availableTillAccess !== 'undefined' && +availableTillAccess['active'] === 1) {
            canPlayPermissions.availTill = true;
            if(new Date(availableTillAccess['timestamp']) < new Date()) {
                _permission['canplay'] = false;
                canPlayPermissions.availTill = false;
            }
        }
        //End

        if(('availFrom' in canPlayPermissions) && ('availTill' in canPlayPermissions)) {
            canPlayPermissions.avail = (canPlayPermissions.availFrom && canPlayPermissions.availTill);
            delete canPlayPermissions.availFrom;
            delete canPlayPermissions.availTill;
        }

        //check rules
        if (typeof access['activities'] !== 'undefined' && Object.keys(access['activities']).length > 0) {
            let overridedPlayPermission = false;
            for (var index in access['activities']) {
                if (access['activities'].hasOwnProperty(index)) {
                    let activity = access['activities'][index];
                    let lecture = (typeof studentLog["log"][activity['lecture_id']] != "undefined") ? studentLog["log"][activity['lecture_id']] : null;//returns log of this lecture
                    switch (activity['action']) {
                        case "complete_with_percentage":
                            canPlayPermissions.courseWPC = true;
                            if (lecture === null || typeof lecture['percentage_of_marks'] == 'undefined' || (lecture['percentage_of_marks'] < activity['percentage'])) {
                                if (typeof this.props.pointableLectures[+activity['lecture_id']] !== 'undefined') {
                                    _permission['canplay'] = false;
                                    canPlayPermissions.courseWPC = false;
                                }
                            } else {
                                overridedPlayPermission = true;
                            }
                            break;
                        case "complete":
                            canPlayPermissions.complete = true;
                            let views = 0;
                            if(lecture) {
                                views = lecture['nviews'] ? lecture['nviews'] : lecture['views'];
                            }
                            if (lecture === null || typeof views == 'undefined' || +views < 1) {
                                if (typeof this.props.pointableLectures[+activity['lecture_id']] !== 'undefined') {
                                    _permission['canplay'] = false;
                                    canPlayPermissions.complete = false;
                                }
                            } else {
                                overridedPlayPermission = true;
                            }
                            break;
                        default:
                    }
                }
                if (overridedPlayPermission === true && access['verify'] === "anyone") {
                    _permission['canplay'] = true;
                    break;
                }
            }
        }

        const canPlayArray = Object.values(canPlayPermissions);
        const canPlayReduced = (canPlayArray.length > 0) ? canPlayArray.reduce((accumulator, currentValue) => (accumulator || currentValue)) : false;

        if(canPlayReduced && (access['verify'] === "anyone")) {
            _permission['canplay'] = true;
        }
        //End
        return _permission;
    }

    render() {

        let lecturesObj = null;

        if(this.state.lectures.length){
            let lastlect = 0;
            this.state.lectures.map(lecture => {
                if(+lecture.cl_lecture_type !== 13){
                    lastlect = +lecture.id;
                }
                return lecture;
            });

            let l_keys = 0;
            lecturesObj = this.state.lectures.map((lecture,l_key)=>{
                if(+lecture.cl_lecture_type !== 13){
                    l_keys++;
                    let locked = false;
                    if ((lecture.cl_access_restriction || +lecture.cl_limited_access) && this.props.lectureLog) {
                        let response = this.canPlayLecture({ ...this.props.lectureLog, 'lecture_id': lecture.id }, { ...JSON.parse(lecture.cl_access_restriction), 'access_limit': lecture.cl_limited_access });
                        if (!response.canplay) {
                            locked = true;
                        }
                    }
                    if(([3,8,10,14].indexOf(+lecture.cl_lecture_type) > -1) && this.props.isPreview) {
                        locked = true
                    }
                    return <Lecture key={lecture.id}
                        first = {l_keys === 1?true:false}
                        last = {(lastlect === +lecture.id)?true:false}
                        lectureName = {lecture.cl_lecture_name}
                        lectureId = {lecture.id}
                        type = {lecture.cl_lecture_type}
                        totalAttempts = {lecture.cl_limited_access}
                        attempts = {lecture.views}
                        percentage = {lecture.percentage}
                        courseId={this.props.courseId}
                        locked={locked}
                        duration={lecture.cl_duration}
                    />;
                }else{
                    return null;
                }
            });
        }
        return (
            <div className={this.props.collapsed?'curriculam-collapse':'curriculam'}>
                {lecturesObj}
            </div>
        );
    }
}


const mapStateToProps = state => {
    return {
        lectureLog: state.lectureLog,
        pointableLectures: state.pointableLectures,
        isPreview: state.isPreview
    };
};

export default connect(mapStateToProps)(withRouter(Lectures));