import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import Swipe from 'react-easy-swipe';
import * as actionTypes from '../../../store/actions';

import Video from '../../../components/content/video/Video';
import Flow from '../../../components/content/video/Flow';
import Recorded from '../../../components/content/recorded/Recorded';
import Document from '../../../components/content/document/Document';
import Html from '../../../components/content/html/Html';
import Youtube from '../../../components/content/youtube/Youtube';
import Mp3 from '../../../components/content/mp3/Mp3';
import Assessment from '../../../components/content/assessment/Assessment';
import Scorm from '../../../components/content/scorm/Scorm';
import Assignment from '../../../components/content/assignment/Assignment';
import Certificate from '../../../components/content/certificate/Certificate';
import Live from '../../../components/content/live/Live';

import Survey from '../../../components/content/survey/Survey';
import Transition from '../../../components/content/transition/Transition';
import Locked from '../../../components/content/locked/Locked';

import Logger from '../../../components/utils/services/logger/Logger';
import { redirectToDashboard } from '../../../components/utils/services/utils';

class Content extends Component {

    mounted = false;

    state = {
        currLecture: null,
        currLectureId: 0,
        lectureLog: null,
        subscription: null,
        showTransition: false,
        autoPlay: true,
        stayHere: false,
        pointableLectures: null,
        loggerProgress: false,
        swipe:false
    }

    componentDidMount() {
        this.mounted = true;

        if (this.mounted) {
            window.onpopstate = () => {
                this.props.lectureSwitch(+this.props.match.params.lectureId);
            }
        }
    }

    componentDidUpdate(prevProps) {
        if (this.mounted && this.props.currLecture && (typeof this.props.currLecture.id !== 'undefined') && (+this.props.currLecture.id !== +this.state.currLectureId)) {
            // console.log(this.props.lectureLog)
            this.setState({
                currLecture: this.props.currLecture,
                currLectureId: this.props.currLecture ? this.props.currLecture.id : 0,
                subscription: this.state.subscription ? this.state.subscription : this.props.subscription,
                lectureLog: this.props.lectureLog,
                pointableLectures: this.props.pointableLectures
            });
        }

        if (this.mounted && this.props.lectureLog.update && !this.props.isPreview) {
            let lectureLog = [];
            let logData = { ...this.props.lectureLog.log };
            for (let key in logData) {
                lectureLog.push({
                    lecture_id: key,
                    percentage: logData[key].percentage,
                    cue: logData[key].cue,
                    views: logData[key].views
                });
            }
            const logParams = {
                courseId: this.props.match.params.courseId,
                subscriptionId: this.props.subscription.id,
                percentage: this.props.lectureLog.overall_percentage,
                lectureType : this.state.currLecture.cl_lecture_type,
                lectureName : this.state.currLecture.cl_lecture_name,
                lectureId : this.state.currLecture.id,
                lectureData: [...lectureLog]
            };
            // console.log(this.mounted, logParams)
            // console.log(this.mounted, this.props.lectureLog.update, lectureLog.length, this.state.loggerProgress)

            if (this.mounted && this.props.lectureLog.update && lectureLog.length && !this.state.loggerProgress) {
                this.props.logComplete();
                this.setState({
                    loggerProgress: true
                }, async () => {
                    try {
                        const result = await Logger(logParams);
                        if(result) {
                            return redirectToDashboard(this.props.subscription.cs_course_id, this.props.isPreview, this.props.courseSlug);
                        }
                    } catch(e) { }
                    this.setState({ loggerProgress: false });
                });
            }
        }
    }

    handleUpdate = (response) => {
        this.setState({
            loggerProgress: false
        });
    }

    componentWillUnmount() {
        this.mounted = false;
    }

    autoPlayCompleteHandler = () => {
        if (this.state.autoPlay && (typeof this.state.currLecture.nexLecture !== 'undefined') && this.state.currLecture.nexLecture.id) {
            if (!this.props.currOpen && [1, 4, 12].indexOf(+this.state.currLecture.cl_lecture_type) !== -1) {
                this.setState({
                    showTransition: true
                });
            }
        }

        // if (!this.props.currOpen && typeof this.state.currLecture.nexLecture !== 'undefined' && [1, 4].indexOf(+this.state.currLecture.nexLecture.cl_lecture_type) === -1) {
        //     this.props.history.push({
        //         pathname: `/${this.props.match.params.courseId}/${this.state.currLecture.nexLecture.id}`,
        //         state: { action : 'forward' }
        //     });
        // }
    }

    switchTransform = (stay = false) => {
        this.setState({
            showTransition: false,
            stayHere: stay
        });
    }

    autoPlayToggleHandler = (status) => {
        this.setState({
            autoPlay: status
        });
    }

    canPlayLecture = (studentLog, access) => {

        const _permission = { canplay: true, reason: [] };
        let canPlayPermissions = {};
        //checking attempt limit exceeded
        if (typeof studentLog['log'][studentLog['lecture_id']] != "undefined") {
            const lecture = studentLog['log'][studentLog['lecture_id']];
            const courseAccessLimit = +access['access_limit'];
            const views = lecture['nviews'] ? lecture['nviews'] : lecture['views'];
            if (courseAccessLimit !== 0 && (views >= courseAccessLimit)) {
                _permission['canplay'] = false;
                _permission['reason'].push('You have completed maximum views of this lecture');
                return _permission;
            }
        }
        //End

        //checking course completion 
        const coursePercentageAccess = access['course_percentage'];
        if (typeof coursePercentageAccess !== 'undefined' && +coursePercentageAccess['active'] === 1) {
            canPlayPermissions.coursePC = true;
            if(parseInt(coursePercentageAccess['percentage']) > parseInt(studentLog['overall_percentage'])) {
                _permission['canplay'] = false;
                _permission['reason'].push('You have to complete ' + coursePercentageAccess['percentage'] + '% of this course');
                canPlayPermissions.coursePC = false;
            }
        }
        //End

        //checking start date and time of access 
        const availableFromAccess = access['available_from'];
        // console.log(availableFromAccess);
        if (typeof availableFromAccess !== 'undefined' && +availableFromAccess['active'] === 1) {
            canPlayPermissions.availFrom = true;
            if(new Date(availableFromAccess['timestamp']) > new Date()) {
                _permission['canplay'] = false;
                _permission['reason'].push('You can view this lecture after ' + this.fomatDate(availableFromAccess['timestamp']));
                canPlayPermissions.availFrom = false;
            }
        }
        //End

        //checking end date and time of access 
        const availableTillAccess = access['available_till'];
        if (typeof availableTillAccess !== 'undefined' && +availableTillAccess['active'] === 1) {
            canPlayPermissions.availTill = true;
            if(new Date(availableTillAccess['timestamp']) < new Date()) {
                _permission['canplay'] = false;
                _permission['reason'].push('This lecture can be viewed until ' + this.fomatDate(availableTillAccess['timestamp']));
                canPlayPermissions.availTill = false;
            }
        }
        //End

        if(('availFrom' in canPlayPermissions) && ('availTill' in canPlayPermissions)) {
            canPlayPermissions.avail = (canPlayPermissions.availFrom && canPlayPermissions.availTill);
            delete canPlayPermissions.availFrom;
            delete canPlayPermissions.availTill;
        }

        //check rules
        if (typeof access['activities'] !== 'undefined' && Object.keys(access['activities']).length > 0) {
            let overridedPlayPermission = false;
            for (var index in access['activities']) {
                if (access['activities'].hasOwnProperty(index)) {
                    let activity = access['activities'][index];
                    let lecture = (typeof studentLog["log"][activity['lecture_id']] != "undefined") ? studentLog["log"][activity['lecture_id']] : null;//returns log of this lecture
                    switch (activity['action']) {
                        case "complete_with_percentage":
                            canPlayPermissions.courseWPC = true;
                            if (lecture === null || typeof lecture['percentage_of_marks'] == 'undefined' || (lecture['percentage_of_marks'] < activity['percentage'])) {
                                if (typeof this.state.pointableLectures[+activity['lecture_id']] !== 'undefined') {
                                    _permission['canplay'] = false;
                                    _permission['reason'].push('You need ' + activity['percentage'] + ' percentage of marks for <b>lecture named ' + this.state.pointableLectures[+activity['lecture_id']].cl_lecture_name + '</b>');
                                    canPlayPermissions.courseWPC = false;
                                }
                            } else {
                                overridedPlayPermission = true;
                            }
                            break;
                        case "complete":
                            canPlayPermissions.complete = true;
                            let views = 0;
                            if(lecture) {
                                views = lecture['nviews'] ? lecture['nviews'] : lecture['views'];
                            }
                            if (lecture === null || typeof views == 'undefined' || +views < 1) {
                                if (typeof this.state.pointableLectures[+activity['lecture_id']] !== 'undefined') {
                                    _permission['canplay'] = false;
                                    _permission['reason'].push('You need to complete <b>lecture named ' + this.state.pointableLectures[+activity['lecture_id']].cl_lecture_name + '</b>');
                                    canPlayPermissions.complete = false;
                                }
                            } else {
                                overridedPlayPermission = true;
                            }
                            break;
                        default:
                    }
                }
                if (overridedPlayPermission === true && access['verify'] === "anyone") {
                    _permission['canplay'] = true;
                    _permission['reason'] = [];
                    break;
                }
            }
        }

        const canPlayArray = Object.values(canPlayPermissions); /* console.log(canPlayPermissions); */
        const canPlayReduced = (canPlayArray.length > 0) ? canPlayArray.reduce((accumulator, currentValue) => (accumulator || currentValue)) : false;

        if(canPlayReduced && (access['verify'] === "anyone")) {
            _permission['canplay'] = true;
            _permission['reason'] = [];
        }
        //End
        return _permission;
    }

    fomatDate = (dateToFormat) => {
        const myDate = new Date(dateToFormat);

        let daysList = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        let monthsList = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Aug', 'Oct', 'Nov', 'Dec'];


        let date = myDate.getDate();
        let month = monthsList[myDate.getMonth()];
        let year = myDate.getFullYear();
        let day = daysList[myDate.getDay()];

        let today = `${date} ${month} ${year}, ${day}`;
        let amOrPm;
        let twelveHours = function () {
            if (myDate.getHours() > 12) {
                amOrPm = 'PM';
                let twentyFourHourTime = myDate.getHours();
                let conversion = twentyFourHourTime - 12;
                return `${conversion}`

            } else {
                amOrPm = 'AM';
                return `${myDate.getHours()}`
            }
        };
        let hours = twelveHours();
        let minutes = ("0" + myDate.getMinutes()).slice(-2);
        let currentTime = `${"0" + hours}:${minutes} ${amOrPm}`;

        return (today + ' ' + currentTime);
    }

    onSwipeStart = (event) => {
        this.setState({
            swipe : true
        });
    }

    onSwipeEnd = (event) => {
        this.setState({
            swipe : false
        });
    }

    render() {
        const config = this.props.config;
        let content = null;
        if (this.state.currLectureId) {
            document.title = config.siteName+' - ' + this.state.currLecture.cl_lecture_name;
            switch (+this.state.currLecture.cl_lecture_type) {
                case 1:
                    if(parseInt(config.content_secuirity) === 1) {
                        content = <Flow
                            swipeEvent={this.state.swipe}
                            onComplete={this.autoPlayCompleteHandler}
                            autoPlay={this.state.stayHere ? !this.state.stayHere : this.state.autoPlay}
                            courseId={this.props.subscription.cs_course_id}
                            key={this.state.currLecture.id}
                            videoId={this.state.currLecture.cl_filename} />
                    } else {
                        content = <Video
                            swipeEvent={this.state.swipe}
                            onComplete={this.autoPlayCompleteHandler}
                            autoPlay={this.state.stayHere ? !this.state.stayHere : this.state.autoPlay}
                            courseId={this.props.subscription.cs_course_id}
                            key={this.state.currLecture.id}
                            videoId={this.state.currLecture.cl_filename} />
                    }
                    break;

                case 2:
                    content = <Document
                        key={this.state.currLecture.id}
                        currOpen={this.props.currOpen}
                        courseId={this.props.subscription.cs_course_id}
                        lectureId={this.state.currLectureId}
                        documentId={this.state.currLecture.cl_org_file_name}
                        swipeEvent={this.state.swipe} />
                    break;

                case 5:
                    content = <Html
                        key={this.state.currLecture.id}
                        lectureId={this.state.currLecture.id}
                        courseId={this.state.subscription.cs_course_id}
                        swipeEvent={this.state.swipe} />
                    break;
                case 4:
                    content = <Youtube
                        key={this.state.currLecture.id}
                        autoPlay={this.state.stayHere ? !this.state.stayHere : this.state.autoPlay}
                        onComplete={this.autoPlayCompleteHandler}
                        courseId={this.props.subscription.cs_course_id}
                        videoId={this.state.currLecture.cl_filename}
                        swipeEvent={this.state.swipe} />
                    break;

                case 12:
                    content = <Mp3
                        key={this.state.currLecture.id}
                        autoPlay={this.state.stayHere ? !this.state.stayHere : this.state.autoPlay}
                        onComplete={this.autoPlayCompleteHandler}
                        lectureId={this.state.currLecture.id}
                        audioId={this.state.currLecture.cl_org_file_name}
                        courseId={this.state.subscription.cs_course_id}
                        swipeEvent={this.state.swipe} />
                    break;
                case 3:
                    content = <Assessment
                        lectureId={this.state.currLectureId}
                        swipeEvent={this.state.swipe}
                    />;
                    break;
                case 10:
                    content = <Scorm
                        lectureId={this.state.currLectureId}
                        swipeEvent={this.state.swipe}
                        courseId={this.props.subscription.cs_course_id}
                    />;
                    break;
                case 7:
                    content = <Live
                        lectureId={this.state.currLectureId}
                        swipeEvent={this.state.swipe}
                        courseId={this.props.subscription.cs_course_id}
                    />;
                    break;
                case 8:
                    content = <Assignment
                        lectureId={this.state.currLectureId}
                        swipeEvent={this.state.swipe}
                    />;
                    break;
                case 13:
                    content = <Survey
                        currOpen={this.props.currOpen}
                        lectureId={this.state.currLectureId}
                        swipeEvent={this.state.swipe}
                    />;
                    break;
                case 14:
                    content = <Certificate
                        currOpen={this.props.currOpen}
                        swipeEvent={this.state.swipe}
                    />
                    break;
                case 11:
                    content = <Recorded
                        currOpen={this.props.currOpen}
                        swipeEvent={this.state.swipe}
                    />
                    break;
                case 15:
                    content = <Video
                        swipeEvent={this.state.swipe}
                        onComplete={this.autoPlayCompleteHandler}
                        autoPlay={this.state.stayHere ? !this.state.stayHere : this.state.autoPlay}
                        courseId={this.props.subscription.cs_course_id}
                        key={this.state.currLecture.id}
                        videoId={this.state.currLecture.cl_filename} />
                    break;
                default:
                    content = null;
            }

            content = <div className="lecture-wrapper">
                {content}
            </div>
        }

        content = !this.state.showTransition ? content : <Transition
            switchTransform={this.switchTransform}
            autoPlay={this.state.autoPlay}
            closeTransition={(stat) => this.switchTransform(stat)}
            autoPlayToggleHandler={(status) => this.autoPlayToggleHandler(status)}
        />;

        if (this.state.currLecture && (this.state.currLecture.cl_access_restriction || +this.state.currLecture.cl_limited_access) && this.state.lectureLog) {
            let response = this.canPlayLecture({ ...this.state.lectureLog, 'lecture_id': this.state.currLectureId }, { ...JSON.parse(this.state.currLecture.cl_access_restriction), 'access_limit': this.state.currLecture.cl_limited_access });
            if (!response.canplay) {
                content = <div className="lecture-wrapper">
                    <Locked
                        reasons={response.reason}
                        currOpen={this.props.currOpen}
                        swipeEvent={this.state.swipe}
                    />
                </div>;
            }
        }

        if (this.state.currLecture && ([3, 8, 10, 14].indexOf(+this.state.currLecture.cl_lecture_type) > -1) && this.props.isPreview) {
            content = <div className="lecture-wrapper">
                    <Locked
                        reasons={['Please purchase the course to view this item.']}
                        currOpen={this.props.currOpen}
                        swipeEvent={this.state.swipe}
                    />
                </div>;
        }

        return (<Swipe
            onSwipeStart={this.onSwipeStart}
            onSwipeMove={this.onSwipeMove}
            onSwipeEnd={this.onSwipeEnd}>
            {content}
        </Swipe>);
    }
}

const mapStateToProps = state => {
    return {
        config: state.config,
        currLecture: state.currLecture,
        subscription: state.subscription,
        currOpen: state.currOpen,
        lectureLog: state.lectureLog,
        pointableLectures: state.pointableLectures,
        isPreview: state.isPreview,
        courseSlug: state.course.cb_slug
    };
};

const mapDispatchToProps = dispatch => {
    return {
        lectureSwitch: (lectureId) => dispatch({ type: actionTypes.SWITCH_LECTURE, payload: { lectureId: lectureId } }),
        logComplete: () => dispatch({ type: actionTypes.UPDATE_COMPLETE })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Content));