import React, {Component} from 'react';
import {connect} from 'react-redux';
import * as actionTypes from '../../../store/actions';
import Aux from '../../../hoc/Aux/Aux';
import DocumentHeader from '../document/documentHeader/DocumentHeader';
import DocumentFooter from '../document/documentFooter/DocumentFooter';
import './Scorm.css';
import ScormIcon from '../../../assets/images/scorm-icon.png';
import {getPreviewDetails} from '../../utils/services/utils';

class Scorm extends Component 
{
    state = {
        opacity : 1,
        timer : null,
        courseId : 0,
        currLecture : null
    }

    componentDidMount(){
        this.mounted = true;
        if(this.mounted && this.props.currLecture){
            this.setState({
                currLecture : {...this.props.currLecture}
            });
            this.mouseLeaveHandler();
        }
        getPreviewDetails(this.props.courseId, this.props.isPreview, this.props.courseSlug).catch(console.log)
    }

    componentWillReceiveProps(nextProps){
        if(this.mounted && nextProps.currLecture && +nextProps.currLecture.id !== +this.props.currLecture.id){
            this.setState({
                currLecture : {...nextProps.currLecture}
            });
        }

        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.mouseEnterHandler();
            }else{
                this.mouseLeaveHandler();
            }
        }
    }

    componentWillUnmount(){
        this.mounted = false;
    }

    mouseEnterHandler = () => {
        if(this.state.timer){
            clearTimeout(this.state.timer);
        }
        if(this.state.mounted){
            this.setState({
                opacity : 1
            });
        }
    }

    mouseLeaveHandler = () =>{
        // if(!this.props.currOpen){
        //     const timer = setTimeout(() => {
        //         if(this.state.mounted){
        //             this.setState({
        //                 opacity : 0
        //             });
        //         }
        //     },4000);
            
        //     if(this.state.mounted){
        //         this.setState({
        //             timer : timer
        //         });
        //     }
        // }
    }

    playScorm = (scormUrl) => {
        window.open(scormUrl, 'Scorm - SDPK', 'toolbar=0,status=0,width='+window.screen.availWidth+',height='+window.screen.availWidth+'');
        this.props.onPercentageUpdate({
            percentage : 100,
            cue : 0,
            lectureId : this.props.lectureId,
            completed : true
        });
    }

    render(){
        const config = this.props.config;
        const scormData = {...this.state.currLecture};
        let content = null;
        if(scormData){
            let scormSource = config.scormUrl.replace("{scormPath}", scormData.cl_filename);
            scormSource = scormSource.replace("{courseId}", this.state.courseId);

            content = <div className="context">
                        <p className="scorm-info">
                                This is a packaged content. Press play button to view the lecture content.
                        </p>
                        <div className="text-center btn-container">
                            <button onClick={() => this.playScorm(scormSource)} className="btn-violet" >Play</button>
                        </div>
                    </div>
        }
        return(
            <Aux >
                <div className="preview-area">
                    <span className="scorm-header"><DocumentHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} /></span>
                    <div className="lecture-holder scorm-holder" id="lecture_holder">
                        <div className="context-holder">
                            <div className="text-center">
                                <img className="lock-img" src={ScormIcon} alt=""/>
                            </div>
                            <h5 className="locked-title scorm-title">Scorm Package</h5>
                            {content}
                        </div>
                    </div>
                    <DocumentFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
                </div>
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        config: state.config,
        courseId : state.subscription.id?state.subscription.cs_course_id:0,
        currOpen : state.currOpen,
        currLecture : state.currLecture?{...state.currLecture}:null,
        isPreview: state.isPreview,
		courseSlug: state.course.cb_slug
    };
}

const mapDispatchToProps = dispatch => {
    return {
		onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData})
	};
};

export default connect(mapStateToProps,mapDispatchToProps)(Scorm);