import Axios from '../../../../axios/php';

const updateLog = (updateParam,callback) => {
    const request = {
        header : {
            method: "update_percentage"
        },
        body : {
            course_id : updateParam.courseId,
            subscriber_id : updateParam.subscriptionId,
            course_percentage : updateParam.percentage,
            last_played : updateParam.lectureId,
            lecture_data : updateParam.lectureData,
            lectureType : updateParam.lectureType,
            lectureName : updateParam.lectureName,
        }
    };


    return Axios.post(`/content`,JSON.stringify(request))
    .then(response => {
        if(!response.data.header) {
            return true;
        }
        if(response.data.header.code === 604 || response.data.header.error){
            return true;
        }

        return false;

        // if(!response.data.header.error){
        //     callback(response.data.header);
        // }else{
        //     if(response.data.header.code === 604){
        //         window.location = redirectUrl;
        //     }else{
        //         //console.log(response.data.header.message);
        //     }
        // }
    })
    .catch(err => {
        return true;
    });
}

export default updateLog;