import React,{ Component } from 'react';
import { connect } from 'react-redux';
import * as actionTypes from '../../../store/actions';
import DocumentHeader from './documentHeader/DocumentHeader';
import DocumentFooter from './documentFooter/DocumentFooter';
import Checkmobile from '../../utils/services/checkmobile/Checkmobile';
import {getPreviewDetails} from '../../utils/services/utils';
import './Document.css';

function reloadIFrame() {
    var iframe = document.getElementById("documentFrame");
    if(iframe.contentDocument && iframe.contentDocument.URL === "about:blank"){
        iframe.src += '';
    }
}

class Document extends Component {
    mounted = false;
    docLoadTimer = null;
    state = {
        opacity : 1,
        timer : null
    }

    componentDidMount(){
        this.mounted = true;
        this.mouseLeaveHandler();
        this.docLoadTimer = setInterval(reloadIFrame, 2000);
        document.getElementById("documentFrame").onload = () => {
            clearInterval(this.docLoadTimer);
            this.props.onPercentageUpdate({
                percentage : 100,
                cue : 0,
                lectureId : this.props.lectureId,
                completed : true
            });
        };
        getPreviewDetails(this.props.courseId, this.props.isPreview, this.props.courseSlug).catch(console.log)
    }

    mouseEnterHandler = () => {
        if (this.state.timer) {
            clearTimeout(this.state.timer);
        }

        if (this.mounted) {
           
            this.setState({
                opacity: 1
            });
            this.props.onOpacityChange("lecture-holder overflow-visible active-doc-over")
        }
    }

    mouseLeaveHandler = () => {
        if (this.mounted && !this.props.currOpen && !this.props.showList && !this.props.isPreview) {
            const timer = setTimeout(() => {
                if (this.mounted) {
                 if(Checkmobile() === false){
                    this.setState({
                        opacity: 0
                    });
                    this.props.onOpacityChange("lecture-holder overflow-visible")
                }
            }
            }, 4000);

            this.setState({
                timer: timer
            });
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.mounted && this.props.swipeEvent !== nextProps.swipeEvent){
            if(nextProps.swipeEvent){
                this.mouseEnterHandler();
            }else{
                this.mouseLeaveHandler();
            }
        }
    }

    componentWillUnmount(){
        this.mounted = false;
        clearInterval(this.docLoadTimer);
    }

    continueFull = () => {
        if(this.mounted){
            this.setState({
                showModal : false
            },() => {
                let el = document.documentElement , rfs = el.requestFullScreen || el.webkitRequestFullScreen || el.mozRequestFullScreen ; rfs.call(el);
            });
        }
    }

    render(){
        let documentUrl = this.props.config.documentUrl;
        const classes = this.state.opacity === 0 ? "preview-area document-preview full-height" : "preview-area document-preview";
        documentUrl = documentUrl.replace('{docId}',this.props.documentId);
        documentUrl = documentUrl.replace('{courseId}',this.props.courseId);
        return(
            <div className={classes}>
                <div className="active-topbar" onMouseEnter={this.mouseEnterHandler} onMouseLeave={this.mouseLeaveHandler}></div>
                <DocumentHeader mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
                <div className={this.props.opacityClass}>
                    <div className="context-holder document-context documentframe-container">
                        <iframe id="documentFrame" ref="documentFrame" title="Document" width="100%" height="100%" style={{border:'none'}} src={documentUrl}></iframe>
                    </div>
                </div>

                

                <DocumentFooter mouseEnterHandler={this.mouseEnterHandler} mouseLeaveHandler={this.mouseLeaveHandler} opacity={this.state.opacity} />
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onPercentageUpdate: (lectureData) => dispatch({type :actionTypes.UPDATE_PERCENTAGE,payload : lectureData}),
        onOpacityChange: (opacityClass) => dispatch({ type: actionTypes.SET_OPACITY_CLASS, payload: opacityClass })
        
	};
};

const mapStateToProps = state => {
    return {
        config: state.config,
        currLecture : state.currLecture,
        currOpen : state.currOpen,
        course : state.course,
        opacityClass: state.opacityClass,
        showList: state.showList,
        isPreview: state.isPreview,
		courseSlug: state.course.cb_slug
    };
};


export default connect(mapStateToProps,mapDispatchToProps)(Document);